/**
 * ==================== src/contexts/WebViewSDKContext.jsx ====================
 * WebView SDK Context Provider - Manages WebView SDK instance
 * 
 * This provider:
 * - Initializes and provides WebViewSDK instance
 * - Shows loading state during initialization
 * - Cleans up SDK resources on unmount
 */

import React, { createContext, useContext, useEffect, useState } from 'react';
import { WebViewSDK } from '../utils/WebViewSDK';
import { saveLogException } from '@logs';

// Create context
const WebViewSDKContext = createContext(null);

/**
 * WebViewSDKProvider Component
 * Provides WebView SDK instance to children
 */
export const WebViewSDKProvider = ({ children }) => {
  // Initialize SDK instance
  const [sdk] = useState(() => new WebViewSDK());
  const [isReady, setIsReady] = useState(false);

  // Initialize SDK and cleanup on unmount
  useEffect(() => {
    const timer = setTimeout(() => setIsReady(true), 100);
    
    return () => {
      clearTimeout(timer);
      sdk.cleanup();
    };
  }, [sdk]);

  // Show loading state during initialization
  if (!isReady) {
    return (
      <div style={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: '100vh',
        fontSize: '14px',
        color: '#64748b'
      }}>
        Loading...
      </div>
    );
  }

  return (
    <WebViewSDKContext.Provider value={sdk}>
      {children}
    </WebViewSDKContext.Provider>
  );
};

/**
 * Hook to access WebView SDK
 * @returns {Object} WebViewSDK instance
 */

export const useWebViewSDK = () => {
  const context = useContext(WebViewSDKContext);
  if (!context) {
    const error = new Error('useWebViewSDK must be used within WebViewSDKProvider');
    
    // Log exception tr∆∞·ªõc khi throw
    saveLogException(error, {
      logSource: 'context',
      logCategory: 'webview_sdk',
      metadata: { hook: 'useWebViewSDK' }
    }).catch(logError => {
      console.error('Failed to log exception:', logError);
    });
    
    throw error;
  }
  return context;
};