import React from 'react';
import '@controls/Text/styles.css'

const Text = (props) => {
  const {styleText, className, children, maxLines, ...restProps} = props;

  const style = maxLines ? {
    display: '-webkit-box',
    WebkitLineClamp: maxLines,
    WebkitBoxOrient: 'vertical',
    overflow: 'hidden',
    textOverflow: 'ellipsis',
  } : {};

  // Process children to convert \n to newlines
  const processChildren = (content) => {
    if (typeof content === 'string') {
      return content.split('\n').map((part, index) => (
        <React.Fragment key={index}>
          {index > 0 && <br />}
          {part}
        </React.Fragment>
      ));
    }
    return content;
  };

  return (
    <span 
      {...restProps} 
      className={`text ${className || ''}`} 
      style={{...style, ...styleText}}
    >
      {processChildren(children)}
    </span>
  )
}

export default Text;