/**
 * ==================== src/contexts/ServiceContext.jsx ====================
 * Service Context Provider - Handles API errors and notifications
 * 
 * This provider:
 * - Manages API errors globally
 * - Provides error notification handlers
 * - Shows error toasts or dialogs
 */

import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';
import { onLoadingChange } from '@services/base';
import { useWebViewSDK } from './WebViewSDKContext';
import { saveLogException } from '@logs';

// Create context
const ServiceContext = createContext(null);

/**
 * ServiceProvider Component
 * Provides API error handling and notification services
 */
export const ServiceProvider = ({ children }) => {
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isErrorDialogOpen, setIsErrorDialogOpen] = useState(false);
  const sdk = useWebViewSDK();

  /**
   * Handle API errors
   * @param {Error} error - The error object
   */
  const handleError = useCallback((error) => {
    console.error('üî¥ [Service] API Error:', error);

    // Set error state
    setError(error);

    // Show error dialog
    setIsErrorDialogOpen(true);
  }, []);

  /**
   * Clear current error
   */
  const clearError = useCallback(() => {
    setError(null);
    setIsErrorDialogOpen(false);
  }, []);

  /**
   * Handle error dialog close
   */
  const handleErrorDialogClose = useCallback(() => {
    // sdk.sendUnknownError(error);
    sdk.backToApp();
    clearError();
  }, [error]);

  const value = {
    error,
    handleError,
    clearError,
    isLoading,
    // Expose dialog controls to render outside the provider
    isErrorDialogOpen,
    setIsErrorDialogOpen,
    onErrorDialogDone: handleErrorDialogClose,
  };

  // Subscribe to global loading changes
  useEffect(() => {
    const unsubscribe = onLoadingChange(setIsLoading);
    return () => unsubscribe();
  }, []);

  return (
    <ServiceContext.Provider value={value}>
      {children}
    </ServiceContext.Provider>
  );
};

/**
 * Hook to access Service Context
 * @returns {Object} Service context with error handling functions
 */
export const useService = () => {
  const context = useContext(ServiceContext);
  if (!context) {
    const error = new Error('useService must be used within ServiceProvider');
    
    // Log exception tr∆∞·ªõc khi throw
    saveLogException(error, {
      logSource: 'context',
      logCategory: 'service',
      metadata: { hook: 'useService' }
    }).catch(logError => {
      console.error('Failed to log exception:', logError);
    });
    
    throw error;
  }
  return context;
};

