import React, { useEffect, useCallback, forwardRef } from 'react';
import { useNavigation } from '../../contexts';
import '@controls/Dialog/styles.css';
import { ICCloseDialog } from '@/assets/icons';
import { Text } from '@/controls';

/**
 * Dialog Component - Dialog hi·ªÉn th·ªã gi·ªØa m√†n h√¨nh
 * 
 * Features:
 * - H·ªó tr·ª£ nhi·ªÅu dialog ch·ªìng l√™n nhau (stacking)
 * - T√≠ch h·ª£p v·ªõi NavigationContext ƒë·ªÉ x·ª≠ l√Ω back gesture
 * - Auto scroll khi n·ªôi dung qu√° d√†i (>80vh)
 * - Click backdrop ƒë·ªÉ ƒë√≥ng dialog (configurable)
 * - N√∫t close t√πy ch·ªçn
 * - H·ªó tr·ª£ custom content component
 * - Hi·ªÉn th·ªã gi·ªØa m√†n h√¨nh tr√™n c·∫£ mobile v√† desktop
 * 
 * Props:
 * @param {string} id - Unique identifier cho dialog (required)
 * @param {boolean} isOpen - Tr·∫°ng th√°i hi·ªÉn th·ªã dialog
 * @param {boolean} isShowClose - Hi·ªÉn th·ªã n√∫t close (default: true)
 * @param {boolean} isBackdropModal - Cho ph√©p click backdrop ƒë·ªÉ ƒë√≥ng (default: true)
 * @param {string} title - Ti√™u ƒë·ªÅ dialog
 * @param {string|ReactNode} content - N·ªôi dung text ho·∫∑c component
 * @param {function} onClose - Callback khi ƒë√≥ng dialog
 * @param {string} size - K√≠ch th∆∞·ªõc dialog: 'small' | 'medium' | 'large' (default: 'medium')
 */
const Dialog = forwardRef(({
  id,
  isOpen = false,
  setOpen,
  isShowClose = true,
  isBackdropModal = true,
  title,
  content,
  footerComponent,
  onClose,
  size = 'medium',
  overlayClassName = '',
  children
}, ref) => {
  const { openModal, closeModal, closeAllModals } = useNavigation();

  // Register/unregister dialog v·ªõi NavigationContext
  useEffect(() => {
    if (isOpen && id) {
      console.log('üî≤ [Dialog] Opening:', id);
      openModal(id);

      // Prevent body scroll khi dialog m·ªü
      document.body.style.overflow = 'hidden';
    }

    return () => {
      if (isOpen) {
        console.log('üî≤ [Dialog] Cleanup:', id);
        document.body.style.overflow = '';
      }
    };
  }, [isOpen, id, openModal]);

  // Handle ESC key
  useEffect(() => {
    const handleEsc = (e) => {
      if (e.key === 'Escape' && isOpen) {
        hide();
      }
    };

    if (isOpen) {
      document.addEventListener('keydown', handleEsc);
    }

    return () => {
      document.removeEventListener('keydown', handleEsc);
    };
  }, [isOpen]);

  // ƒê√≥ng dialog hi·ªán t·∫°i
  const hide = useCallback(() => {
    console.log('üî≤ [Dialog] Hiding:', id);
    closeModal();

    // delay remove stack modal navigate
    setTimeout(() => {
      setOpen(false);
      onClose?.();
    }, 100)
  }, [id, closeModal, onClose]);

  // ƒê√≥ng t·∫•t c·∫£ dialog
  const hideAll = useCallback(() => {
    console.log('üî≤ [Dialog] Hiding all dialogs');
    closeAllModals();
    onClose?.();
  }, [closeAllModals, onClose]);

  // Handle backdrop click
  const handleBackdropClick = useCallback((e) => {
    if (isBackdropModal && e.target.classList.contains('dialog-overlay')) {
      hide();
    }
  }, [isBackdropModal, hide]);

  // Expose methods via imperative handle (if needed)
  React.useImperativeHandle(ref, () => ({
    hide,
    hideAll
  }));

  useEffect(() => {
    if (!isOpen) {
      hide();
    }
  }, [isOpen, hide]);

  if (!isOpen) return null;

  return (
    <div
      className={`dialog-overlay ${overlayClassName}`}
      onClick={handleBackdropClick}
    >
      <div className={`dialog-container dialog-${size}`}>
        {/* Header */}
        {(title || isShowClose) && (
          <div className="dialog-header">
            {title && <Text className="text-size-18 text-weight-600">{title}</Text>}
            {isShowClose && (
              <button
                className="dialog-close-btn"
                onClick={hide}
                aria-label="Close dialog"
              >
                <ICCloseDialog />
              </button>
            )}
          </div>
        )}

        {/* Content */}
        <div className="dialog-content">
          <div className="dialog_container">
            {content && (<div className="dialog_content">
              <Text className="text-size-14 text-black-60p">{content}</Text>
            </div>
            )}
            {children}
            {footerComponent && (
              <div className="dialog_footer">
                {footerComponent}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
});

export default Dialog;