import { ICExchangeFailed } from "@/assets/icons";
import { useLanguage } from "@/contexts/LanguageContext";
import { Button, Dialog, Text } from "@/controls";
import "@components/DialogError/styles.css";

const DialogError = (props) => {
  const { isOpen, setOpen, onDone } = props;
  const { t } = useLanguage();

  const handleDone = () => {
    setOpen(false);
    onDone();
  }

  const footerComponent = (
    <>
      <Button isFullWidth type="primary" onClick={handleDone}>
        {t("common_close")}
      </Button>
    </>
  );

  return (
    <Dialog
      id="error-dialog"
      isShowClose={false}
      footerComponent={footerComponent}
      isBackdropModal={false}
      isOpen={isOpen}
      setOpen={setOpen}
      overlayClassName="error-dialog-overlay"
    >
        <div className="error-dialog-body">
          <div><ICExchangeFailed /></div>

          <Text className="text-size-18 text-weight-600 text-neutral-1000 text-align-center">
            {t("common_error")}
          </Text>

          <Text className="text-size-14 text-weight-400 text-neutral-700 text-align-center">
            {t("common_error_description")}
          </Text>
          <div className="error-dialog-hotline">
            <Text className="text-size-14 text-weight-400 text-neutral-700 text-align-center">
              {t("common_hotline")}:
            </Text>
            <Text className="text-size-16 text-weight-700 text-black text-align-center">
              {t("common_hotline_number")}
            </Text>
          </div>

        </div>
      </Dialog>
  );
};

export default DialogError;