// ==================== src/utils/WebViewSDK.jsx ====================
// WebView SDK - Bridge gi·ªØa WebView v√† Native App (iOS Swift & Android Kotlin)
// Updated: X·ª≠ l√Ω Native Swipe Back Gesture ‚Üí Game Navigation

import { saveLogException } from '@logs';

/**
 * WebViewSDK Class
 * X·ª≠ l√Ω communication gi·ªØa WebView v√† Native App
 * H·ªó tr·ª£: 
 * - iOS WKWebView (v·ªõi swipe back gesture)
 * - Android WebView
 * - Browser (Mock mode)
 */
export class WebViewSDK {
  constructor() {
    // Detect m√¥i tr∆∞·ªùng WebView
    this.isInWebView = this.detectWebView();
    this.isInViewInMobile = this.detechViewInMobile();

    // Map l∆∞u tr·ªØ message handlers theo action
    this.messageHandlers = new Map();

    // Map l∆∞u tr·ªØ pending callbacks (ch·ªù response t·ª´ native)
    this.pendingCallbacks = new Map();

    // Counter ƒë·ªÉ t·∫°o unique callback ID
    this.callbackId = 0;

    // ƒê·ªãnh nghƒ©a t·∫•t c·∫£ actions ƒë∆∞·ª£c support
    this.ACTIONS = {
      BACK_APP: 'BACK_APP',              // 1. ƒê√≥ng WebView, quay v·ªÅ native
      SAVE_SECURE: 'SAVE_SECURE',        // 2. L∆∞u data v√†o Keychain/SecureStorage
      GET_SECURE: 'GET_SECURE',          // 3. L·∫•y data t·ª´ Keychain/SecureStorage
      DELETE_SECURE: 'DELETE_SECURE',    // 4. X√≥a data t·ª´ Keychain/SecureStorage
      GET_TOKEN: 'GET_TOKEN',            // 5. L·∫•y auth token
      GET_USER_INFO: 'GET_USER_INFO',    // 6. L·∫•y th√¥ng tin user
      GET_DEVICE_INFO: 'GET_DEVICE_INFO',// 7. L·∫•y th√¥ng tin device
      WEB_READY: 'WEB_READY',            // Signal: WebView ƒë√£ ready

      // === NEW: NATIVE SWIPE BACK GESTURE ===
      NATIVE_SWIPE_BACK: 'NATIVE_SWIPE_BACK', // native g·ª≠i event khi user vu·ªët tr√°i

      // INIT WEB_SDK
      WEB_TO_SDK: 'WEB_TO_SDK',

      // ERROR DIALOG
      UNKNOWN_ERROR: 'UNKNOWN_ERROR',
    };

    // Callback ƒë·ªÉ x·ª≠ l√Ω swipe back navigation
    this.onSwipeBackHandler = null;

    // Kh·ªüi t·∫°o SDK
    this.init();
  }

  /**
   * Kh·ªüi t·∫°o SDK
   * - ƒêƒÉng k√Ω message listener
   * - G·ª≠i WEB_READY signal ƒë·∫øn native
   */
  init() {
    // ƒêƒÉng k√Ω listener ƒë·ªÉ nh·∫≠n message t·ª´ native ho·∫∑c parent window
    const shouldListenToMessages = this.isInViewInMobile || window.parent !== window;
    
    if (shouldListenToMessages) {
      window.addEventListener('message', this.handleNativeMessage.bind(this));

      // Th√¥ng b√°o cho native app ho·∫∑c parent window r·∫±ng WebView ƒë√£ s·∫µn s√†ng
      this.sendToNative({
        action: this.ACTIONS.WEB_READY,
        timestamp: Date.now()
      });

      console.log('üåâ WebView SDK initialized - Platform:', this.getPlatform());
      
      if (this.isInWebView) {
        console.log('üëÜ Native Swipe Back Gesture support enabled');
      } else if (window.parent !== window) {
        console.log('üåê Running in iframe mode (Parent-Child communication enabled)');
      }
    } else {
      console.log('üåê Running in browser standalone mode (Mock responses enabled)');
    }
  }

  /**
   * Detect m√¥i tr∆∞·ªùng WebView
   * @returns {boolean} true n·∫øu ƒëang ch·∫°y trong WebView
   */
  detectWebView() {
    // Check Android WebView
    // Android inject object "Android" v√†o window v·ªõi method postMessage
    if (window.Android && typeof window.Android.postMessage === 'function') {
      console.log('üì± Detected: Android WebView (Kotlin)');
      return true;
    }

    // Check iOS WKWebView
    // iOS inject webkit.messageHandlers.nativeApp v√†o window
    if (window.webkit?.messageHandlers?.nativeApp) {
      console.log('üçé Detected: iOS WKWebView (Swift)');
      return true;
    }

    // Kh√¥ng ph·∫£i WebView, ch·∫°y trong browser
    return false;
  }

  detechViewInMobile() {
    // Check screen width < 1024px
    if (window.innerWidth < 1024) {
      return true;
    }

    // Kh√¥ng ph·∫£i WebView, ch·∫°y trong browser desktop
    return false;
  }

  /**
   * G·ª≠i message ƒë·∫øn Native App
   * @param {Object} payload - Data c·∫ßn g·ª≠i
   */
  sendToNative(payload) {
    // N·∫øu ƒëang ch·∫°y trong iframe (browser), g·ª≠i message l√™n parent window
    if (!this.isInWebView && window.parent !== window) {
      console.log('üì± [Browser Iframe Mode] Sending to parent window:', payload);
      window.parent.postMessage(payload, '*');
      return;
    }

    // N·∫øu ƒëang ch·∫°y trong browser standalone, d√πng mock response
    if (!this.isInWebView) {
      console.log('üì± [Browser Standalone Mode] Would send to native:', payload);
      this.mockNativeResponse(payload);
      return;
    }

    try {
      // === G·ª¨I T·ªöI ANDROID KOTLIN WEBVIEW ===
      if (window.Android?.postMessage) {
        // Android y√™u c·∫ßu stringify JSON
        const message = JSON.stringify(payload);
        window.Android.postMessage(message);
        console.log('üì§ [Web ‚Üí Android] Sent:', payload.action, payload);
      }

      // === G·ª¨I T·ªöI iOS SWIFT WKWEBVIEW ===
      else if (window.webkit?.messageHandlers?.nativeApp) {
        // iOS WKWebView c√≥ th·ªÉ g·ª≠i tr·ª±c ti·∫øp object
        window.webkit.messageHandlers.nativeApp.postMessage(payload);
        console.log('üì§ [Web ‚Üí iOS Swift] Sent:', payload.action, payload);
      }

      else {
        const error = new Error('No native bridge found');
        
        // Log exception tr∆∞·ªõc khi throw
        saveLogException(error, {
          logSource: 'webview',
          logCategory: 'native_bridge',
          metadata: { action: payload?.action }
        }).catch(logError => {
          console.error('Failed to log exception:', logError);
        });
        
        throw error;
      }

    } catch (error) {
      console.error('‚ùå Error sending to native:', error);
      
      const newError = new Error('Failed to communicate with native app: ' + error.message);
      
      // Log exception tr∆∞·ªõc khi throw
      saveLogException(newError, {
        logSource: 'webview',
        logCategory: 'native_communication',
        metadata: { action: payload?.action, originalError: error.message }
      }).catch(logError => {
        console.error('Failed to log exception:', logError);
      });
      
      throw newError;
    }
  }

  /**
   * X·ª≠ l√Ω message nh·∫≠n ƒë∆∞·ª£c t·ª´ Native App
   * @param {MessageEvent} event - Message event t·ª´ native
   * 
   * iOS Swift ContentView s·∫Ω g·ª≠i c√°c lo·∫°i message:
   * 
   * 1. Callback Response:
   * {
   *   success: true/false,
   *   data: any,
   *   error: string (optional),
   *   callbackId: string
   * }
   * 
   * 2. Swipe Back Gesture Event:
   * {
   *   action: "NATIVE_SWIPE_BACK",
   *   timestamp: number,
   *   gesture: "swipe_left"
   * }
   */
  handleNativeMessage(event) {
    try {      
      // Parse message (c√≥ th·ªÉ l√† string ho·∫∑c object)
      const message = typeof event.data === 'string'
        ? JSON.parse(event.data)
        : event.data;

      console.log('üì• [Native ‚Üí Web] Received:', message);

      // === X·ª¨ L√ù Native Swipe Back GESTURE ===
      if (message.action === this.ACTIONS.NATIVE_SWIPE_BACK) {
        console.log('üëÜ [Native Swipe Back] Detected swipe left gesture');
        this.handleSwipeBack(message);
        return;
      }

      // === X·ª¨ L√ù Init SDK trong WebView ===
      if (message.action === this.ACTIONS.WEB_TO_SDK) {
        console.log('üëÜ [WebView INIT SDK]');
        this.handleWebToSDK(message);
        return;
      }


      // === X·ª¨ L√ù CALLBACK RESPONSE ===
      // N·∫øu message c√≥ callbackId, ƒë√¢y l√† response cho request tr∆∞·ªõc ƒë√≥
      if (message.callbackId && this.pendingCallbacks.has(message.callbackId)) {
        const { resolve, reject, timeout } = this.pendingCallbacks.get(message.callbackId);

        // Clear timeout
        clearTimeout(timeout);

        // Remove callback kh·ªèi pending list
        this.pendingCallbacks.delete(message.callbackId);

        // Resolve ho·∫∑c reject promise d·ª±a tr√™n success flag
        if (message.success) {
          console.log('‚úÖ Callback resolved:', message.callbackId, message.data);
          resolve(message.data);
        } else {
          console.error('‚ùå Callback rejected:', message.callbackId, message.error);
          reject(new Error(message.error || 'Native operation failed'));
        }
      }

      // === X·ª¨ L√ù CUSTOM MESSAGE HANDLERS ===
      // N·∫øu message c√≥ action v√† c√≥ handler ƒëƒÉng k√Ω cho action ƒë√≥
      else if (message.action && this.messageHandlers.has(message.action)) {
        const handler = this.messageHandlers.get(message.action);
        console.log('üéØ Calling custom handler for:', message.action);
        handler(message.data);
      }

      // === MESSAGE KH√îNG X·ª¨ L√ù ƒê∆Ø·ª¢C ===
      else {
        console.warn('‚ö†Ô∏è Unhandled message from native:', message);
      }

    } catch (error) {
      console.error('‚ùå Error handling native message:', error, event.data);
    }
  }

  /**
   * === X·ª¨ L√ù Init WebView SDK ===
   * @param {Object} message - Message t·ª´ iOS
   */
  handleWebToSDK(message) {
    window.WEB_TO_SDK = message?.params;
  }

  /**
   * === X·ª¨ L√ù Native Swipe Back GESTURE ===
   * 
   * Khi user vu·ªët tr√°i m√†n h√¨nh tr√™n iOS:
   * 1. iOS Swift ContentView detect swipe gesture
   * 2. iOS g·ª≠i message NATIVE_SWIPE_BACK v·ªÅ WebView
   * 3. WebView SDK nh·∫≠n message v√† g·ªçi handler
   * 4. Handler s·∫Ω navigate back trong Game app
   * 
   * @param {Object} message - Message t·ª´ iOS
   */
  handleSwipeBack(message) {
    console.log('üëÜ [Swipe Back Handler] Processing...', message);
    // G·ªçi custom handler n·∫øu ƒë√£ ƒëƒÉng k√Ω
    if (this.onSwipeBackHandler && typeof this.onSwipeBackHandler === 'function') {
      console.log('üîÑ [Swipe Back] Calling custom handler');
      this.onSwipeBackHandler(message);
    }
  }

  /**
   * === ƒêƒÇNG K√ù HANDLER CHO SWIPE BACK ===
   * 
   * Game app s·∫Ω ƒëƒÉng k√Ω handler n√†y ƒë·ªÉ x·ª≠ l√Ω navigation
   * khi user vu·ªët tr√°i tr√™n iOS
   * 
   * @param {Function} handler - Function ƒë∆∞·ª£c g·ªçi khi swipe back
   * 
   * Example trong Game:
   * ```javascript
   * sdk.onSwipeBack((message) => {
   *   // Check current screen
   *   if (game.isRootScreen()) {
   *     // ·ªû trang ch·ªß ‚Üí ƒë√≥ng WebView
   *     sdk.backToApp();
   *   } else {
   *     // ·ªû trang detail ‚Üí navigate back trong app
   *     game.navigateBack();
   *   }
   * });
   * ```
   */
  onSwipeBack(handler) {
    if (typeof handler !== 'function') {
      const error = new Error('Swipe back handler must be a function');
      
      // Log exception tr∆∞·ªõc khi throw
      saveLogException(error, {
        logSource: 'webview',
        logCategory: 'swipe_back',
        metadata: { handlerType: typeof handler }
      }).catch(logError => {
        console.error('Failed to log exception:', logError);
      });
      
      throw error;
    }

    this.onSwipeBackHandler = handler;
    console.log('üìå [Swipe Back] Handler registered');
  }

  /**
   * H·ªßy ƒëƒÉng k√Ω swipe back handler
   */
  offSwipeBack() {
    this.onSwipeBackHandler = null;
    console.log('üìå [Swipe Back] Handler unregistered');
  }

  /**
   * ƒêƒÉng k√Ω custom message handler
   * @param {string} action - Action name
   * @param {Function} handler - Handler function
   */
  on(action, handler) {
    this.messageHandlers.set(action, handler);
    console.log('üìå Registered handler for action:', action);
  }

  /**
   * H·ªßy ƒëƒÉng k√Ω message handler
   * @param {string} action - Action name
   */
  off(action) {
    this.messageHandlers.delete(action);
    console.log('üìå Unregistered handler for action:', action);
  }

  /**
   * G·ª≠i message v√† ƒë·ª£i callback response
   * @param {string} action - Action name
   * @param {*} data - Data to send (optional)
   * @param {number} timeout - Timeout in ms (default: 10000)
   * @returns {Promise} Promise resolve v·ªõi data t·ª´ native
   */
  sendWithCallback(action, data = null, timeout = 10000) {
    return new Promise((resolve, reject) => {
      // T·∫°o unique callback ID
      const id = `cb_${++this.callbackId}_${Date.now()}`;

      // T·∫°o timeout ƒë·ªÉ auto-reject n·∫øu native kh√¥ng response
      const timeoutId = setTimeout(() => {
        if (this.pendingCallbacks.has(id)) {
          this.pendingCallbacks.delete(id);
          console.error('‚è±Ô∏è Callback timeout:', id, action);
          reject(new Error(`Timeout waiting for native response: ${action}`));
        }
      }, timeout);

      // L∆∞u callback v√†o pending list
      this.pendingCallbacks.set(id, { resolve, reject, timeout: timeoutId });

      // G·ª≠i message k√®m callbackId
      const payload = {
        action,
        data,
        callbackId: id,
        timestamp: Date.now()
      };

      console.log('‚è≥ Waiting for callback:', id, action);
      this.sendToNative(payload);
    });
  }

  // ==================== 7 PUBLIC API METHODS ====================

  /**
   * 1. BACK_APP - ƒê√≥ng WebView v√† quay v·ªÅ Native App
   * 
   * iOS Swift ContentView s·∫Ω nh·∫≠n action n√†y v√†:
   * - Dismiss WebView
   * - Return v·ªÅ previous screen
   */
  backToApp() {
    console.log('üîô Closing WebView...');
    this.sendToNative({
      action: this.ACTIONS.BACK_APP,
      timestamp: Date.now()
    });
  }

  /**
   * 2. SAVE_SECURE - L∆∞u data v√†o Keychain (iOS) / EncryptedSharedPreferences (Android)
   * @param {string} key - Key ƒë·ªÉ l∆∞u
   * @param {string} value - Value c·∫ßn l∆∞u
   * @returns {Promise<void>}
   */
  async saveSecure(key, value) {
    console.log('üíæ Saving secure data:', key);
    return this.sendWithCallback(this.ACTIONS.SAVE_SECURE, { key, value });
  }

  /**
   * 3. GET_SECURE - L·∫•y data t·ª´ Keychain (iOS) / EncryptedSharedPreferences (Android)
   * @param {string} key - Key c·∫ßn l·∫•y
   * @returns {Promise<string|null>} Value ho·∫∑c null n·∫øu kh√¥ng t√¨m th·∫•y
   */
  async getSecure(key) {
    console.log('üîç Getting secure data:', key);
    return this.sendWithCallback(this.ACTIONS.GET_SECURE, { key });
  }

  /**
   * 4. DELETE_SECURE - X√≥a data t·ª´ Keychain (iOS) / EncryptedSharedPreferences (Android)
   * @param {string} key - Key c·∫ßn x√≥a
   * @returns {Promise<void>}
   */
  async deleteSecure(key) {
    console.log('üóëÔ∏è Deleting secure data:', key);
    return this.sendWithCallback(this.ACTIONS.DELETE_SECURE, { key });
  }

  /**
   * 5. GET_TOKEN - L·∫•y authentication token
   * @returns {Promise<string|null>}
   */
  async getToken() {
    console.log('üîë Getting auth token...');
    return this.sendWithCallback(this.ACTIONS.GET_TOKEN);
  }

  /**
   * 6. GET_USER_INFO - L·∫•y th√¥ng tin user hi·ªán t·∫°i
   * @returns {Promise<Object>} User info object
   */
  async getUserInfo() {
    console.log('üë§ Getting user info...');
    return this.sendWithCallback(this.ACTIONS.GET_USER_INFO);
  }

  /**
   * 7. GET_DEVICE_INFO - L·∫•y th√¥ng tin device
   * @returns {Promise<Object>} Device info object
   */
  async getDeviceInfo() {
    // console.log('üì± Getting device info...');
    // return this.sendWithCallback(this.ACTIONS.GET_DEVICE_INFO);
  }

  /**
   * 8. UNKNOWN_ERROR - Hi·ªán th·ªã dialog error
   * @returns {Promise<void>}
   */
  async sendUnknownError(error) {
    console.log('üî¥ Sending unknown error...');
    return this.sendWithCallback(this.ACTIONS.UNKNOWN_ERROR, { error });
  }

  // ==================== MOCK RESPONSES (Browser Mode) ====================

  /**
   * Mock native responses khi ch·∫°y trong browser
   * D√πng ƒë·ªÉ test trong m√¥i tr∆∞·ªùng development
   */
  mockNativeResponse(payload) {
    // Simulate async response v·ªõi setTimeout
    setTimeout(() => {
      const mockResponses = {
        // Mock token response
        [this.ACTIONS.GET_TOKEN]: {
          success: true,
          data: 'mock_token_abc123xyz_' + Date.now()
        },

        // Mock user info response
        [this.ACTIONS.GET_USER_INFO]: {
          success: true,
          data: {
            id: '12345',
            name: 'Test User',
            email: 'test@example.com',
            avatar: 'https://ui-avatars.com/api/?name=Test+User',
            phone: '+84 123 456 789',
            role: 'user'
          }
        },

        // Mock device info response
        [this.ACTIONS.GET_DEVICE_INFO]: {
          success: true,
          data: {
            platform: 'browser',
            version: '1.0.0',
            os: navigator.platform,
            language: navigator.language,
            userAgent: navigator.userAgent,
            screenWidth: window.screen.width,
            screenHeight: window.screen.height
          }
        },

        // Mock get secure response
        [this.ACTIONS.GET_SECURE]: {
          success: true,
          data: '{"mock":"data","timestamp":' + Date.now() + '}'
        },

        // Mock save secure response
        [this.ACTIONS.SAVE_SECURE]: {
          success: true,
          data: null
        },

        // Mock delete secure response
        [this.ACTIONS.DELETE_SECURE]: {
          success: true,
          data: null
        }
      };

      // N·∫øu c√≥ callbackId v√† c√≥ mock response cho action n√†y
      if (payload.callbackId && mockResponses[payload.action]) {
        const response = {
          ...mockResponses[payload.action],
          callbackId: payload.callbackId
        };

        console.log('üé≠ [Mock] Sending response:', response);

        // Simulate response t·ª´ native b·∫±ng postMessage
        window.postMessage(response, '*');
      }
    }, 500); // Delay 500ms ƒë·ªÉ gi·ªëng th·∫≠t
  }

  // ==================== UTILITY METHODS ====================

  /**
   * Ki·ªÉm tra xem app c√≥ ƒëang ch·∫°y trong WebView kh√¥ng
   * @returns {boolean}
   */
  isNativeWebView() {
    return this.isInWebView;
  }

  isViewInMobile() {
    return this.isInViewInMobile;
  }

  /**
   * L·∫•y platform hi·ªán t·∫°i
   * @returns {'android'|'ios'|'browser'}
   */
  getPlatform() {
    if (window.Android) return 'android';
    if (window.webkit?.messageHandlers?.nativeApp) return 'ios';
    return 'browser';
  }

  /**
   * Cleanup - X√≥a t·∫•t c·∫£ listeners v√† pending callbacks
   * G·ªçi khi component unmount
   */
  cleanup() {
    console.log('üßπ Cleaning up WebView SDK...');

    // Remove event listener
    window.removeEventListener('message', this.handleNativeMessage);

    // Clear message handlers
    this.messageHandlers.clear();

    // Clear swipe back handler
    this.onSwipeBackHandler = null;

    // Clear all pending callbacks v√† timeouts
    this.pendingCallbacks.forEach(({ timeout }) => clearTimeout(timeout));
    this.pendingCallbacks.clear();

    console.log('‚úÖ WebView SDK cleaned up');
  }
}

// ==================== EXPORTS ====================

// Export singleton instance - s·ª≠ d·ª•ng tr·ª±c ti·∫øp
export const sdk = new WebViewSDK();

// Export class - ƒë·ªÉ t·∫°o instance m·ªõi n·∫øu c·∫ßn
export default WebViewSDK;

// ==================== GLOBAL ACCESS ====================
// Expose SDK globally ƒë·ªÉ c√≥ th·ªÉ d√πng trong console ho·∫∑c inline scripts
if (typeof window !== 'undefined') {
  window.webViewSDK = sdk;

  // Alias ƒë·ªÉ g·ªçi d·ªÖ d√†ng h∆°n
  window.nativeApp = {
    // 7 actions c∆° b·∫£n
    backToApp: () => sdk.backToApp(),
    saveSecure: (key, value) => sdk.saveSecure(key, value),
    getSecure: (key) => sdk.getSecure(key),
    deleteSecure: (key) => sdk.deleteSecure(key),
    getToken: () => sdk.getToken(),
    getUserInfo: () => sdk.getUserInfo(),
    getDeviceInfo: () => sdk.getDeviceInfo(),

    // Utility methods
    isNativeWebView: () => sdk.isNativeWebView(),
    isViewInMobile: () => sdk.isViewInMobile(),
    getPlatform: () => sdk.getPlatform(),

    // === NEW: SWIPE BACK HANDLER ===
    onSwipeBack: (handler) => sdk.onSwipeBack(handler),
    offSwipeBack: () => sdk.offSwipeBack()
  };

  console.log('‚úÖ Global access enabled: window.webViewSDK, window.nativeApp');
  console.log('üëÜ Native Swipe Back: Use window.nativeApp.onSwipeBack(handler)');
}