/**
 * ==================== src/contexts/LanguageContext.jsx ====================
 * Language Context Provider - Manages application language and translations
 * 
 * Features:
 * - Supports Vietnamese (vi) and English (en)
 * - Persists language preference in localStorage
 * - Dispatches language change events
 * - Provides translation function with optional formatting
 */

import React, { createContext, useContext, useState, useCallback } from 'react';
import vi from '@localize/vi';
import en from '@localize/en';
import { saveLogException } from '@logs';

// Available translations
const translations = { vi, en };

// Create context
const LanguageContext = createContext(null);

/**
 * LanguageProvider Component
 * Provides language state and translation function to children
 */
export const LanguageProvider = ({ children }) => {
  // Initialize language from localStorage or default to Vietnamese
  const [currentLang, setCurrentLang] = useState(() => {
    const saved = localStorage.getItem('game-sdk-language');
    console.log('üåç [LanguageContext] Initial language from localStorage:', saved || 'vi');
    return saved || 'vi';
  });

  // Log when currentLang changes
  React.useEffect(() => {
    console.log('üåç [LanguageContext] currentLang changed to:', currentLang);
  }, [currentLang]);

  /**
   * Change application language
   * @param {string} lang - Language code ('vi' or 'en')
   */
  const setLanguage = useCallback((lang) => {
    console.log('üåç [LanguageContext] setLanguage called with:', lang);
    if (translations[lang]) {
      setCurrentLang(lang);
      localStorage.setItem('game-sdk-language', lang);
      console.log('üåç [LanguageContext] Language updated to:', lang);
      console.log('üåç [LanguageContext] localStorage updated:', localStorage.getItem('game-sdk-language'));

      // Dispatch event for other components
      window.dispatchEvent(new CustomEvent('languageChanged', {
        detail: { language: lang }
      }));
    } else {
      console.warn('üåç [LanguageContext] Invalid language:', lang);
    }
  }, []);

  /**
   * Translation function with interpolation
   * @param {string} key - Translation key
   * @param {string|null} action - Optional formatting: 'FirstCharUppercase'
   * @param {Object} params - Optional params for {placeholders}
   * @returns {string} Translated text or key if not found
   */
  const t = useCallback((key, action = null, params = undefined) => {
    const raw = translations[currentLang]?.[key];
    const safe = typeof raw === 'string' ? raw : (raw ?? key);
    
    const interpolate = (text, p) => {
      if (!p) return text;
      try {
        return text.replace(/\{(\w+)\}/g, (_, k) => {
          const v = p[k];
          return v === null || v === undefined ? '' : String(v);
        });
      } catch (_) {
        return text;
      }
    };
    
    let value = interpolate(safe, params);
    
    if (action === 'FirstCharUppercase' && typeof value === 'string' && value.length > 0) {
      value = value.charAt(0).toUpperCase() + value.slice(1);
    }
    
    return value;
  }, [currentLang]);

  const value = {
    currentLang,
    setLanguage,
    t,
    translations: translations[currentLang]
  };

  return (
    <LanguageContext.Provider value={value}>
      {children}
    </LanguageContext.Provider>
  );
};

export const useLanguage = () => {
  const context = useContext(LanguageContext);
  if (!context) {
    const error = new Error('useLanguage must be used within LanguageProvider');
    
    // Log exception tr∆∞·ªõc khi throw
    saveLogException(error, {
      logSource: 'context',
      logCategory: 'language',
      metadata: { hook: 'useLanguage' }
    }).catch(logError => {
      console.error('Failed to log exception:', logError);
    });
    
    throw error;
  }
  return context;
};
