import React, { useEffect, useState } from "react";
import "@controls/Button/styles.css";
import { useLanguage } from "@/contexts";
import { Text } from "@/controls";
import { BtnBackgroundPrimary, BtnBackgroundSecondary } from "@/assets/images";

const Button = (props) => {
  const { type, onClick, isFullWidth = false, disabled, classButton, classText, children } = props;
  const { t } = useLanguage();

  const BackgroundComponent = type === 'primary' ? BtnBackgroundPrimary : BtnBackgroundSecondary;

  return (
    <button disabled={disabled} onClick={onClick} className={`
      button-style
      button-${type ?? 'primary'}
      ${isFullWidth ? 'button-full-width' : ''}
      ${disabled ? 'button-disabled' : ''}
      ${classButton}
    `}>
      <Text className={`
        text-size-24 
        text-weight-900
        ${type === 'primary' ? 'text-white' : ''}
        ${type === 'secondary' ? 'text-red' : ''}
        ${classText}
      `}>{children}</Text>
      <BackgroundComponent className="background-image" />

    </button>
  );
}

export default Button;