import { BtnBackIcon, BtnMute, BtnMuteInactive } from "@/assets/images";
import { useLanguage } from "@/contexts";
import { useAudioPlayer } from "@/hooks/AudioPlayer";
import "@components/Header/styles.css";
import { useState, useEffect } from "react";

const Header = (props) => {
  const KEY = 'game-sdk-muted';
  const { t } = useLanguage();
  const [isMuted, setIsMuted] = useState(() => {
    const saved = localStorage.getItem(KEY);
    return saved ? JSON.parse(saved) : false;
  });
  const {setVolume} = useAudioPlayer();
  const {
    onBack,
  } = props;

  // Load volume state from localStorage on mount
  useEffect(() => {
    setVolume(isMuted ? 0 : 1);
  }, []);

  // Save to localStorage when isMuted changes
  useEffect(() => {
    localStorage.setItem(KEY, JSON.stringify(isMuted));
  }, [isMuted]);

  const handleMute = async () => {    
    const newMutedState = !isMuted;
    setIsMuted(newMutedState);
    
    // Get audio instance
    const audioInstance = window.audioInstance;
    
    if (audioInstance) {
      try {
        const newVolume = newMutedState ? 0 : 1;
        audioInstance.volume = newVolume;
        
        // On iOS Safari, volume changes may not apply immediately
        // We need to trigger a play/pause to ensure the change takes effect
        const wasPlaying = !audioInstance.paused;
        
        if (wasPlaying) {
          // If audio is playing, pause and resume to apply volume change on iOS
          audioInstance.pause();
          if (!newMutedState) {
            // Resume playing if unmuting
            await audioInstance.play().catch(err => {
              console.warn('Failed to resume audio after unmute:', err);
            });
          }
        } else if (!newMutedState) {
          // If audio is paused and we're unmuting, try to play
          // This also helps unlock audio context on iOS if needed
          await audioInstance.play().catch(err => {
            console.warn('Failed to play audio after unmute:', err);
          });
        }
      } catch (error) {
        console.error('Error handling mute:', error);
        // Fallback: just set volume
        audioInstance.volume = newMutedState ? 0 : 1;
      }
    } else {
      // Fallback to setVolume if audio instance not found
      setVolume(newMutedState ? 0 : 1);
    }
  };

  return (
    <header>
      <div className="header-container">
        <button onClick={onBack} className="header-btn">
          <BtnBackIcon />
        </button>
        <button onClick={handleMute} className="header-btn">
          {isMuted ? <BtnMuteInactive /> : <BtnMute />}
        </button>
      </div>
    </header>
  );
};

export default Header;


