/**
 * ==================== src/services/base.jsx ====================
 * Base API Service - Centralized API call handling
 * 
 * This service:
 * - Handles all API calls (GET, POST, PUT, DELETE)
 * - Adds authentication token from localStorage
 * - Uses baseUrl from environment variables
 * - Provides error handling via ServiceContext
 */

import { saveLogException } from '@logs';

// ==================== GLOBAL LOADING EVENTS ====================
let loadingListeners = [];

const notifyLoading = (isLoading) => {
  try {
    loadingListeners.forEach((listener) => {
      try { listener(isLoading); } catch (_) { /* noop */ }
    });
  } catch (_) { /* noop */ }
};

export const onLoadingChange = (listener) => {
  loadingListeners.push(listener);
  return () => {
    loadingListeners = loadingListeners.filter((l) => l !== listener);
  };
};

/**
 * Get access token from token object
 * @returns {string|null} Access token or null
 */
const getAccessToken = () => {
  try {
    // Token gi·ªù l√† object, l·∫•y accessToken t·ª´ token.accessToken
    const token = window.PARENT_PARAMS?.token;
    if (token && typeof token === 'object' && token.accessToken) {
      return token.accessToken;
    }
    // Fallback: n·∫øu token l√† string (backward compatibility)
    if (typeof token === 'string') {
      return token;
    }
    return null;
  } catch (error) {
    console.error('Error getting ACCESS_TOKEN:', error);
    return null;
  }
};

/**
 * Get base URL from environment variable
 * Priority: window.env (from k8s) > import.meta.env (from .env.production)
 * @returns {string} Base URL for API
 */
const getBaseUrl = () => {
  let baseUrl = '';
    
  // Override: Check KEY LOCALSTORAGE IS_MOCK_MODE IS TRUE
  if (localStorage.getItem('IS_MOCK_MODE') === 'true') {
    // Priority: window.env (k8s) > import.meta.env (.env.production)
    baseUrl = window?.env?.VITE_MOCK_END_POINT || import.meta.env.VITE_MOCK_END_POINT || '';
    console.log('baseUrl from IS_MOCK_MODE:', baseUrl);
    return baseUrl;
  }
  
  // Priority: window.env (from k8s) > import.meta.env (from .env.production)
  baseUrl = window?.env?.VITE_END_POINT || import.meta.env.VITE_END_POINT || '';
  console.log('baseUrl from env (priority: k8s > .env.production):', baseUrl);
  return baseUrl;
};

/**
 * Decode JWT token to get payload
 * @param {string} token - JWT token string
 * @returns {Object|null} Decoded payload or null
 */
const decodeJWT = (token) => {
  try {
    if (!token || typeof token !== 'string') {
      return null;
    }
    
    // JWT format: header.payload.signature
    const parts = token.split('.');
    if (parts.length !== 3) {
      return null;
    }
    
    // Decode payload (base64url)
    const payload = parts[1];
    // Replace URL-safe base64 characters
    const base64 = payload.replace(/-/g, '+').replace(/_/g, '/');
    // Add padding if needed
    const padded = base64 + '='.repeat((4 - base64.length % 4) % 4);
    
    // Decode base64
    const decoded = atob(padded);
    return JSON.parse(decoded);
  } catch (error) {
    console.error('Error decoding JWT:', error);
    return null;
  }
};

/**
 * Get CIF from decoded JWT token
 * @returns {string|null} CIF value or null
 */
const getCIFFromToken = () => {
  try {    
    const accessToken = getAccessToken();
    if (!accessToken) {
      return null;
    }
    
    const decoded = decodeJWT(accessToken);
    if (decoded) {
      console.log('decoded', decoded);
      
      // H·ªó tr·ª£ c·∫£ lowercase v√† uppercase
      return decoded.cif || decoded.CIF || null;
    }
    
    return null;
  } catch (error) {
    console.error('Error getting CIF from token:', error);
    return null;
  }
};

/**
 * Compute member identifier pairs to inject if missing from a container
 * It respects existing keys (both Member.* and MemberKey.*) and won't override them.
 * @param {Object} container
 * @returns {Object} key-value pairs to add (possibly empty)
 */
const getMemberPairsIfMissing = (container = {}) => {
  try {
    const memberKey = 'CIF';
    // L·∫•y CIF t·ª´ decoded JWT token
    const memberValue = getCIFFromToken();
    console.log('memberValue', memberValue);
    
    if (memberValue === null || memberValue === undefined) return {};

    const hasKey = container['MemberKey.Key'] !== undefined || container['MemberKey.Key'] !== undefined;
    const hasValue = container['MemberKey.Value'] !== undefined || container['MemberKey.Value'] !== undefined;

    const toAdd = {};
    if (!hasKey) toAdd['MemberKey.Key'] = memberKey;
    if (!hasValue) toAdd['MemberKey.Value'] = memberValue;
    return toAdd;
  } catch (_) {
    return {};
  }
};

/**
 * Build full URL with query parameters
 * @param {string} endpoint - API endpoint
 * @param {Object} params - Query parameters
 * @returns {string} Full URL with query string
 */
const buildUrl = (endpoint, params = {}) => {
  const baseUrl = getBaseUrl();
  
  // Parse baseUrl to preserve its path
  const baseUrlObj = new URL(baseUrl);
  
  // Remove leading slash from endpoint if present to preserve baseUrl path
  const cleanEndpoint = endpoint.startsWith('/') ? endpoint.slice(1) : endpoint;
  
  // Build URL by combining baseUrl path with endpoint
  const fullPath = baseUrlObj.pathname.endsWith('/') 
    ? `${baseUrlObj.pathname}${cleanEndpoint}`
    : `${baseUrlObj.pathname}/${cleanEndpoint}`;
  
  const url = new URL(fullPath, baseUrlObj.origin);
  
  // Inject member identifiers if missing
  const extraPairs = getMemberPairsIfMissing(params);
  
  Object.keys(extraPairs).forEach((key) => url.searchParams.append(key, extraPairs[key]));

  // Add query parameters
  Object.keys(params).forEach(key => {
    if (params[key] !== null && params[key] !== undefined) {
      url.searchParams.append(key, params[key]);
    }
  });
  
  return url.toString();
};

/**
 * Build headers with authentication token
 * @param {Object} additionalHeaders - Additional headers to include
 * @returns {Object} Request headers
 */
const buildHeaders = (additionalHeaders = {}) => {
  const headers = {
    'Content-Type': 'application/json',
    'Accept-Language': localStorage.getItem('game-sdk-language') || 'vi',
    ...additionalHeaders,
  };

  // Add authorization token if available
  const token = getAccessToken();
  if (token) {
    headers['Authorization'] = `Bearer ${token}`;
  }

  return headers;
};

/**
 * Override attributes with "*En" suffix into original attributes when language is "en"
 * @param {any} obj - Object to process
 * @returns {any} Processed object with overridden attributes
 */
const overridePropsWithEn = (obj) => {
  function processObject(o) {
    if (typeof o !== 'object' || o === null) return o;
    if (Array.isArray(o)) {
      return o.map(item => processObject(item)); // Process each item in an array
    }
    let newObj = {};
    for (let key in o) {
      if (!o.hasOwnProperty(key)) continue;
      const enKey = key + 'En';
      if (o.hasOwnProperty(enKey) && o[enKey] !== null && o[enKey] !== undefined) {
        newObj[key] = o[enKey]; // Override only if "En" exists and is not null/undefined
      } else {
        newObj[key] = processObject(o[key]); // Keep original if "En" is missing/null/undefined
      }
    }
    return newObj;
  }

  return processObject(obj);
};

/**
 * Handle API response
 * @param {Response} response - Fetch response
 * @returns {Promise} Parsed JSON response
 */
const handleResponse = async (response) => {
  if (!response.ok) {
    const error = new Error(`HTTP error! status: ${response.status}`);
    error.status = response.status;
    error.response = await response.json().catch(() => ({}));
    
    // Log exception tr∆∞·ªõc khi throw
    try {
      await saveLogException(error, {
        logSource: 'api',
        logCategory: 'response',
        metadata: { status: response.status, url: response.url }
      });
    } catch (logError) {
      // Ignore log errors ƒë·ªÉ tr√°nh mask original error
      console.error('Failed to log exception:', logError);
    }
    
    throw error;
  }
  
  const data = await response.json();
  
  // Override attributes with "*En" suffix if current language is "en"
  const currentLanguage = localStorage.getItem('game-sdk-language') || 'vi';
  if (currentLanguage === 'en') {
    return overridePropsWithEn(data);
  }
  
  return data;
};

// ==================== LOGGING HELPERS ====================
const formatTimestamp = () => {
  const now = new Date();
  const pad = (n) => String(n).padStart(2, '0');
  const year = now.getFullYear();
  const month = pad(now.getMonth() + 1);
  const day = pad(now.getDate());
  const hours = pad(now.getHours());
  const minutes = pad(now.getMinutes());
  const seconds = pad(now.getSeconds());
  return `${year}${month}${day}${hours}${minutes}${seconds}`;
};

const logRequest = (method, endpoint, headers, payload, ts) => {
  const requestTs = ts || formatTimestamp();
  try {
    console.log(`üì° ${requestTs}_${method}_${endpoint}, headers:`, headers, ', body:', payload);
  } catch (_) {
    console.log(`üì° ${requestTs}_${method}_${endpoint}`);
  }
};

const logSuccess = (method, endpoint, data, ts) => {
  const requestTs = ts || formatTimestamp();
  try {
    console.log(`‚úÖ ${requestTs}_${method}_${endpoint}. data success:`, data);
  } catch (_) {
    console.log(`‚úÖ ${requestTs}_${method}_${endpoint}. data success`);
  }
};

const logError = (method, endpoint, error, ts) => {
  const requestTs = ts || formatTimestamp();
  try {
    console.error(`‚ùå${requestTs}_${method}_${endpoint}`, error);
  } catch (_) {
    console.error(`‚ùå${requestTs}_${method}_${endpoint}`);
  }
};

/**
 * Build memberKeys for POST/PUT body without overriding existing values
 * Format:
 * {
 *   memberKeys: { CIF: "xxxxx" }
 * }
 */
const getMemberKeysForBody = (body = {}) => {
  try {
    // L·∫•y CIF t·ª´ decoded JWT token
    const memberValue = getCIFFromToken();
    console.log('memberValue', memberValue);
    
    if (memberValue === null || memberValue === undefined) return {};

    const existingMemberKeys = body.memberKeys || {};
    if (existingMemberKeys.CIF) {
      return { memberKeys: existingMemberKeys };
    }
    return { memberKeys: { ...existingMemberKeys, CIF: memberValue } };
  } catch (_) {
    return {};
  }
};

/**
 * GET request
 * @param {string} endpoint - API endpoint
 * @param {Object} params - Query parameters
 * @param {Object} options - Additional fetch options
 * @returns {Promise} API response
 */
export const get = async (endpoint, params = {}, options = {}) => {
  const enableLoading = options?.showLoading !== false;
  try {
    if (enableLoading) notifyLoading(true);
    const url = buildUrl(endpoint, params);
    const headers = buildHeaders(options.headers);
    const ts = formatTimestamp();
    
    logRequest('GET', endpoint, headers, params, ts);

    const response = await fetch(url, {
      method: 'GET',
      headers,
      ...options,
    });

    const data = await handleResponse(response);
    logSuccess('GET', endpoint, data, ts);
    return data;
  } catch (error) {
    // try to use the same timestamp if available; if not, generate inside helper
    logError('GET', endpoint, error);
    
    // Log exception tr∆∞·ªõc khi throw
    try {
      await saveLogException(error, {
        logSource: 'api',
        logCategory: 'get',
        metadata: { endpoint, params }
      });
    } catch (logError) {
      // Ignore log errors ƒë·ªÉ tr√°nh mask original error
      console.error('Failed to log exception:', logError);
    }
    
    throw error;
  } finally {
    if (enableLoading) notifyLoading(false);
  }
};

/**
 * POST request
 * @param {string} endpoint - API endpoint
 * @param {Object} body - Request body
 * @param {Object} options - Additional fetch options
 * @returns {Promise} API response
 */
export const post = async (endpoint, body = {}, options = {}) => {
  const enableLoading = options?.showLoading !== false;
  try {
    if (enableLoading) notifyLoading(true);
    const baseUrl = getBaseUrl();
    // Parse baseUrl to preserve its path
    const baseUrlObj = new URL(baseUrl);
    // Remove leading slash from endpoint if present to preserve baseUrl path
    const cleanEndpoint = endpoint.startsWith('/') ? endpoint.slice(1) : endpoint;
    // Build URL by combining baseUrl path with endpoint
    const fullPath = baseUrlObj.pathname.endsWith('/') 
      ? `${baseUrlObj.pathname}${cleanEndpoint}`
      : `${baseUrlObj.pathname}/${cleanEndpoint}`;
    const url = new URL(fullPath, baseUrlObj.origin).toString();
    const headers = buildHeaders(options.headers);
    const ts = formatTimestamp();
    
    // Inject memberKeys into body (format: { memberKeys: { CIF: "xxxxx" } })
    const mergedBody = { ...body, ...getMemberKeysForBody(body) };

    logRequest('POST', endpoint, headers, mergedBody, ts);

    const response = await fetch(url, {
      method: 'POST',
      headers,
      body: JSON.stringify(mergedBody),
      ...options,
    });

    const data = await handleResponse(response);
    logSuccess('POST', endpoint, data, ts);
    return data;
  } catch (error) {
    logError('POST', endpoint, error);
    
    // Log exception tr∆∞·ªõc khi throw
    try {
      await saveLogException(error, {
        logSource: 'api',
        logCategory: 'post',
        metadata: { endpoint, body: mergedBody }
      });
    } catch (logError) {
      // Ignore log errors ƒë·ªÉ tr√°nh mask original error
      console.error('Failed to log exception:', logError);
    }
    
    throw error;
  } finally {
    if (enableLoading) notifyLoading(false);
  }
};

