/**
 * ==================== src/logs/index.jsx ====================
 * Log Management Module - X·ª≠ l√Ω l∆∞u, l·∫•y, x√≥a th√¥ng tin log
 * 
 * Ch·ª©c nƒÉng:
 * - L∆∞u danh s√°ch c√°c ng√†y c√≥ log v√†o "WEBVIEW_LOYALTY_LOG_LIST"
 * - L∆∞u log theo ng√†y v√†o key "LOS_YYYYMMDD"
 * - L·∫•y danh s√°ch log theo ng√†y
 * - X√≥a log theo ng√†y
 * - X·ª≠ l√Ω exception v√† t·ª± ƒë·ªông l∆∞u log
 */

import { sdk } from '../utils/WebViewSDK';

// Constants
const LOG_LIST_KEY = 'WEBVIEW_LOYALTY_LOG_LIST';
const LOG_PREFIX = 'LOS_';

/**
 * Get access token from token object
 * @returns {string|null} Access token or null
 */
const getAccessToken = () => {
  try {
    // Token gi·ªù l√† object, l·∫•y accessToken t·ª´ token.accessToken
    const token = window.PARENT_PARAMS?.token;
    if (token && typeof token === 'object' && token.accessToken) {
      return token.accessToken;
    }
    // Fallback: n·∫øu token l√† string (backward compatibility)
    if (typeof token === 'string') {
      return token;
    }
    return null;
  } catch (error) {
    console.error('Error getting ACCESS_TOKEN:', error);
    return null;
  }
};

/**
 * Decode JWT token to get payload
 * @param {string} token - JWT token string
 * @returns {Object|null} Decoded payload or null
 */
const decodeJWT = (token) => {
  try {
    if (!token || typeof token !== 'string') {
      return null;
    }
    
    // JWT format: header.payload.signature
    const parts = token.split('.');
    if (parts.length !== 3) {
      return null;
    }
    
    // Decode payload (base64url)
    const payload = parts[1];
    // Replace URL-safe base64 characters
    const base64 = payload.replace(/-/g, '+').replace(/_/g, '/');
    // Add padding if needed
    const padded = base64 + '='.repeat((4 - base64.length % 4) % 4);
    
    // Decode base64
    const decoded = atob(padded);
    return JSON.parse(decoded);
  } catch (error) {
    console.error('Error decoding JWT:', error);
    return null;
  }
};

/**
 * Decode JWT token ƒë·ªÉ l·∫•y CIF code l√†m userId
 * @returns {string|null} CIF code ho·∫∑c null
 */
const getUserIdFromToken = () => {
  try {
    const accessToken = getAccessToken();
    if (!accessToken) {
      return null;
    }
    
    const decoded = decodeJWT(accessToken);
    if (decoded) {
      // H·ªó tr·ª£ c·∫£ lowercase v√† uppercase
      return decoded.cif || decoded.CIF || null;
    }
    
    return null;
  } catch (error) {
    console.error('Error getting userId from token:', error);
    return null;
  }
};

/**
 * L·∫•y th√¥ng tin device t·ª´ browser/native
 * @returns {Promise<Object>} Device info
 */
const getDeviceInfo = async () => {
  try {
    const deviceInfo = await sdk.getDeviceInfo();
    return deviceInfo || {};
  } catch (error) {
    console.error('Error getting device info:', error);
    return {};
  }
};

/**
 * Generate unique log ID
 * @returns {string} Unique log ID
 */
const generateLogId = () => {
  return `log_${Date.now()}_${Math.random().toString(36).substring(2, 15)}`;
};

/**
 * Generate session ID
 * @returns {string} Session ID
 */
const generateSessionId = () => {
  const timestamp = Date.now();
  const random = Math.random().toString(36).substring(2, 9);
  return `sess_${random}_${timestamp}`;
};

/**
 * L·∫•y device fingerprint (browser ID)
 * @returns {string} Device ID
 */
const getDeviceId = () => {
  try {
    // L∆∞u device ID v√†o sessionStorage n·∫øu ch∆∞a c√≥
    let deviceId = sessionStorage.getItem('device_id');
    if (!deviceId) {
      deviceId = `browser_${navigator.userAgent.slice(0, 20)}_${Date.now()}`;
      sessionStorage.setItem('device_id', deviceId);
    }
    return deviceId;
  } catch (error) {
    return `browser_unknown_${Date.now()}`;
  }
};

/**
 * Format timestamp th√†nh YYYYMMDD
 * @param {number} timestamp - Unix timestamp (optional, default: now)
 * @returns {string} Date string in YYYYMMDD format
 */
const formatDateKey = (timestamp = Date.now()) => {
  const date = new Date(timestamp);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}${month}${day}`;
};

/**
 * Format timestamp th√†nh YYYYMMDDHHMMSSSSS
 * @param {number} timestamp - Unix timestamp (optional, default: now)
 * @returns {string} Formatted timestamp
 */
const formatCreatedAt = (timestamp = Date.now()) => {
  const date = new Date(timestamp);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');
  const seconds = String(date.getSeconds()).padStart(2, '0');
  const milliseconds = String(date.getMilliseconds()).padStart(3, '0');
  return `${year}${month}${day}${hours}${minutes}${seconds}${milliseconds}`;
};

/**
 * L·∫•y th√¥ng tin m√¥i tr∆∞·ªùng
 * @returns {string} Environment name
 */
const getEnvironment = () => {
  try {
    const baseUrl = import.meta.env.VITE_END_POINT || '';
    if (baseUrl.includes('localhost') || baseUrl.includes('127.0.0.1')) {
      return 'development';
    }
    if (baseUrl.includes('qa') || baseUrl.includes('test')) {
      return 'staging';
    }
    return 'production';
  } catch (error) {
    return 'production';
  }
};

/**
 * Detect device model t·ª´ user agent
 * @returns {string} Device model
 */
const detectDeviceModel = () => {
  try {
    const ua = navigator.userAgent;
    if (ua.includes('iPhone')) return 'iPhone';
    if (ua.includes('iPad')) return 'iPad';
    if (ua.includes('Android')) {
      const match = ua.match(/Android\s([\d.]+)/);
      return `Android ${match ? match[1] : ''}`;
    }
    if (ua.includes('Chrome')) return 'Chrome';
    if (ua.includes('Firefox')) return 'Firefox';
    if (ua.includes('Safari')) return 'Safari';
    if (ua.includes('Edge')) return 'Edge';
    return 'Unknown Browser';
  } catch (error) {
    return 'Unknown';
  }
};

/**
 * Detect OS name v√† version
 * @returns {Object} { osName, osVersion }
 */
const detectOS = () => {
  try {
    const ua = navigator.userAgent;
    let osName = 'Unknown';
    let osVersion = '';

    if (ua.includes('Windows NT')) {
      osName = 'Windows';
      const match = ua.match(/Windows NT ([\d.]+)/);
      osVersion = match ? match[1] : '';
    } else if (ua.includes('Mac OS X')) {
      osName = 'macOS';
      const match = ua.match(/Mac OS X ([\d_]+)/);
      osVersion = match ? match[1].replace(/_/g, '.') : '';
    } else if (ua.includes('Android')) {
      osName = 'Android';
      const match = ua.match(/Android ([\d.]+)/);
      osVersion = match ? match[1] : '';
    } else if (ua.includes('iPhone') || ua.includes('iPad')) {
      osName = 'iOS';
      const match = ua.match(/OS ([\d_]+)/);
      osVersion = match ? match[1].replace(/_/g, '.') : '';
    } else if (ua.includes('Linux')) {
      osName = 'Linux';
    }

    return { osName, osVersion };
  } catch (error) {
    return { osName: 'Unknown', osVersion: '' };
  }
};

/**
 * L·∫•y browser version
 * @returns {string} Browser version
 */
const getBrowserVersion = () => {
  try {
    const ua = navigator.userAgent;
    if (ua.includes('Chrome')) {
      const match = ua.match(/Chrome\/([\d.]+)/);
      return match ? match[1] : '';
    }
    if (ua.includes('Firefox')) {
      const match = ua.match(/Firefox\/([\d.]+)/);
      return match ? match[1] : '';
    }
    if (ua.includes('Safari') && !ua.includes('Chrome')) {
      const match = ua.match(/Version\/([\d.]+)/);
      return match ? match[1] : '';
    }
    if (ua.includes('Edge')) {
      const match = ua.match(/Edge\/([\d.]+)/);
      return match ? match[1] : '';
    }
    return '';
  } catch (error) {
    return '';
  }
};

/**
 * Detect network type (browser limited capability)
 * @returns {string} Network type
 */
const getNetworkType = () => {
  try {
    if ('connection' in navigator) {
      const connection = navigator.connection || navigator.mozConnection || navigator.webkitConnection;
      return connection?.effectiveType || 'unknown';
    }
    return 'unknown';
  } catch (error) {
    return 'unknown';
  }
};

/**
 * Build log object t·ª´ exception ho·∫∑c log data
 * @param {Error|Object} error - Error object ho·∫∑c log data
 * @param {Object} options - Additional options
 * @returns {Promise<Object>} Log object
 */
const buildLogObject = async (error, options = {}) => {
  try {
    const now = Date.now();
    const userId = getUserIdFromToken();
    const deviceInfo = await getDeviceInfo();
    const { osName, osVersion } = detectOS();
    const deviceModel = detectDeviceModel();
    const deviceVersion = getBrowserVersion();
    const environment = getEnvironment();
    const appVersion = '1.0.0'; // From package.json

    // Extract error information
    let logContent = '';
    let errorCode = null;
    let stackTrace = null;
    let logLevel = 'ERROR';
    let logSource = options.logSource || 'webview';
    let logCategory = options.logCategory || 'general';
    let url = window.location.href || '';
    let metadata = options.metadata || {};
    let tags = options.tags || [];

    if (error instanceof Error) {
      logContent = error.message || String(error);
      errorCode = error.code || error.name || 'ERROR';
      stackTrace = error.stack || null;
    } else if (typeof error === 'object' && error !== null) {
      logContent = error.logContent || error.message || JSON.stringify(error);
      errorCode = error.errorCode || error.code || null;
      stackTrace = error.stackTrace || error.stack || null;
      logLevel = error.logLevel || 'ERROR';
      logSource = error.logSource || logSource;
      logCategory = error.logCategory || logCategory;
      url = error.url || url;
      metadata = { ...metadata, ...(error.metadata || {}) };
      tags = [...tags, ...(error.tags || [])];
    } else {
      logContent = String(error);
    }
    
    // Th√™m console content v√†o logContent n·∫øu c√≥ (t·ª´ options.consoleContent)
    // N·ªôi dung console hi·ªán t·∫°i trong source s·∫Ω ƒë∆∞a v√†o logContent
    if (options.consoleContent) {
      logContent = logContent 
        ? `${logContent}\nConsole: ${options.consoleContent}`
        : `Console: ${options.consoleContent}`;
    }

    // Build log object (ch·ªâ include c√°c field c√≥ gi√° tr·ªã ƒë·ªÉ gi·∫£m dung l∆∞·ª£ng)
    const logObject = {
      id: generateLogId(),
      timestamp: now,
      createdAt: formatCreatedAt(now),
      logLevel: logLevel,
      logSource: logSource,
      logCategory: logCategory,
      logContent: logContent,
      url: url,
    };

    // Ch·ªâ th√™m c√°c field n·∫øu c√≥ gi√° tr·ªã
    if (userId) logObject.userId = userId;
    if (errorCode) logObject.errorCode = errorCode;
    if (stackTrace) logObject.stackTrace = stackTrace;
    
    // Device info
    logObject.deviceId = getDeviceId();
    logObject.deviceModel = deviceModel;
    if (deviceVersion) logObject.deviceVersion = deviceVersion;
    logObject.osName = osName;
    if (osVersion) logObject.osVersion = osVersion;
    
    // App info
    logObject.appVersion = appVersion;
    logObject.environment = environment;
    
    // Session info
    logObject.sessionId = options.sessionId || generateSessionId();
    
    // Browser info
    logObject.userAgent = navigator.userAgent || '';
    if (screen.width && screen.height) {
      logObject.screenResolution = `${screen.width}x${screen.height}`;
    }
    if (window.innerWidth && window.innerHeight) {
      logObject.viewport = `${window.innerWidth}x${window.innerHeight}`;
    }
    
    // Network
    const networkType = getNetworkType();
    if (networkType !== 'unknown') logObject.networkType = networkType;
    
    // Log index
    logObject.logIndex = logLevel;
    
    // Optional fields
    if (options.previousLogId) logObject.previousLogId = options.previousLogId;
    if (typeof options.isResolved === 'boolean') logObject.isResolved = options.isResolved;
    if (tags.length > 0) logObject.tags = tags;
    if (Object.keys(metadata).length > 0) logObject.metadata = metadata;
    
    // Performance (n·∫øu c√≥)
    if (options.performance && typeof options.performance === 'object') {
      logObject.performance = options.performance;
    }

    return logObject;
  } catch (err) {
    // Fallback log object n·∫øu c√≥ l·ªói khi build
    console.error('Error building log object:', err);
    return {
      id: generateLogId(),
      timestamp: Date.now(),
      createdAt: formatCreatedAt(),
      logLevel: 'ERROR',
      logSource: 'logs',
      logCategory: 'system',
      logContent: `Error building log: ${err.message}`,
      url: window.location.href || '',
    };
  }
};

/**
 * L·∫•y danh s√°ch c√°c ng√†y c√≥ log
 * @returns {Promise<string[]>} Array of date strings (YYYYMMDD)
 */
export const getLogDateList = async () => {
  try {
    const data = await sdk.getSecure(LOG_LIST_KEY);
    if (!data) {
      return [];
    }
    const list = JSON.parse(data);
    return Array.isArray(list) ? list : [];
  } catch (error) {
    console.error('Error getting log date list:', error);
    // L∆∞u exception n√†y
    await saveLogException(error, { logSource: 'logs', logCategory: 'getLogDateList' });
    return [];
  }
};

/**
 * Th√™m ng√†y v√†o danh s√°ch log dates n·∫øu ch∆∞a c√≥
 * @param {string} dateKey - Date key (YYYYMMDD)
 * @returns {Promise<void>}
 */
const addDateToList = async (dateKey) => {
  try {
    const currentList = await getLogDateList();
    if (!currentList.includes(dateKey)) {
      currentList.push(dateKey);
      // Sort descending (m·ªõi nh·∫•t ƒë·∫ßu ti√™n)
      currentList.sort((a, b) => b.localeCompare(a));
      const jsonString = JSON.stringify(currentList);
      await sdk.saveSecure(LOG_LIST_KEY, jsonString);
    }
  } catch (error) {
    console.error('Error adding date to list:', error);
    // L∆∞u exception n√†y nh∆∞ng kh√¥ng throw ƒë·ªÉ tr√°nh loop
    try {
      await saveLogException(error, { logSource: 'logs', logCategory: 'addDateToList' });
    } catch (_) {
      // Ignore nested errors
    }
  }
};

/**
 * L·∫•y danh s√°ch log theo ng√†y
 * @param {string} dateKey - Date key (YYYYMMDD), optional (default: today)
 * @returns {Promise<Object[]>} Array of log objects
 */
export const getLogsByDate = async (dateKey = null) => {
  try {
    // debugger;
    const targetDate = dateKey || formatDateKey();
    const logKey = `${LOG_PREFIX}${targetDate}`;
    const data = await sdk.getSecure(logKey);
    
    if (!data) {
      return [];
    }
    
    const logs = JSON.parse(data);
    return Array.isArray(logs) ? logs : [];
  } catch (error) {
    console.error('Error getting logs by date:', error);
    // L∆∞u exception n√†y
    await saveLogException(error, { logSource: 'logs', logCategory: 'getLogsByDate' });
    return [];
  }
};

/**
 * L∆∞u log v√†o storage
 * @param {Error|Object} error - Error object ho·∫∑c log data
 * @param {Object} options - Additional options
 * @returns {Promise<Object>} Saved log object
 */
export const saveLog = async (error, options = {}) => {
  // debugger;
  try {
    const logObject = await buildLogObject(error, options);
    const dateKey = formatDateKey(logObject.timestamp);
    const logKey = `${LOG_PREFIX}${dateKey}`;
    
    // L·∫•y danh s√°ch log hi·ªán t·∫°i c·ªßa ng√†y
    const existingLogs = await getLogsByDate(dateKey);
    
    // Th√™m log m·ªõi v√†o ƒë·∫ßu array
    const updatedLogs = [logObject, ...existingLogs];
    
    // L∆∞u l·∫°i danh s√°ch log
    const jsonString = JSON.stringify(updatedLogs);
    await sdk.saveSecure(logKey, jsonString);
    
    // Th√™m dateKey v√†o danh s√°ch n·∫øu ch∆∞a c√≥
    await addDateToList(dateKey);
    
    return logObject;
  } catch (saveError) {
    // N·∫øu l∆∞u log th·∫•t b·∫°i, log v√†o console
    console.error('Error saving log:', saveError);
    console.error('Original error:', error);
    
    // C·ªë g·∫Øng l∆∞u exception n√†y (nh∆∞ng kh√¥ng d√πng saveLog ƒë·ªÉ tr√°nh loop)
    try {
      const fallbackLog = {
        id: generateLogId(),
        timestamp: Date.now(),
        createdAt: formatCreatedAt(),
        logLevel: 'ERROR',
        logSource: 'logs',
        logCategory: 'saveLog',
        logContent: `Failed to save log: ${saveError.message}\nOriginal: ${error instanceof Error ? error.message : String(error)}`,
        url: window.location.href || '',
      };
      const dateKey = formatDateKey();
      const logKey = `${LOG_PREFIX}${dateKey}`;
      const existingLogs = await getLogsByDate(dateKey).catch(() => []);
      existingLogs.unshift(fallbackLog);
      await sdk.saveSecure(logKey, JSON.stringify(existingLogs));
      await addDateToList(dateKey);
    } catch (_) {
      // Ignore nested errors
    }
    
    throw saveError;
  }
};

/**
 * L∆∞u exception log (wrapper v·ªõi logLevel ERROR)
 * @param {Error} error - Error object
 * @param {Object} options - Additional options
 * @returns {Promise<Object>} Saved log object
 */
/**
 * Capture console content t·ª´ error object ho·∫∑c capture t·ª´ console
 * @param {Error|Object} error - Error object
 * @returns {string} Console content
 */
const getConsoleContent = (error) => {
  try {
    // N·∫øu error c√≥ consoleContent property
    if (error && typeof error === 'object' && error.consoleContent) {
      return String(error.consoleContent);
    }
    
    // N·∫øu error c√≥ message, d√πng message l√†m console content
    if (error && typeof error === 'object' && error.message) {
      return String(error.message);
    }
    
    // N·∫øu error l√† string
    if (typeof error === 'string') {
      return error;
    }
    
    // Fallback: stringify error
    return JSON.stringify(error);
  } catch (err) {
    return String(error);
  }
};

/**
 * L∆∞u exception log (wrapper v·ªõi logLevel ERROR)
 * @param {Error} error - Error object
 * @param {Object} options - Additional options
 * @returns {Promise<Object>} Saved log object
 */
export const saveLogException = async (error, options = {}) => {
  // debugger;
  const consoleContent = getConsoleContent(error);
  
  return await saveLog(error, {
    ...options,
    logLevel: 'ERROR',
    consoleContent: consoleContent,
  });
};

/**
 * X√≥a log theo ng√†y
 * @param {string} dateKey - Date key (YYYYMMDD)
 * @returns {Promise<void>}
 */
export const deleteLogsByDate = async (dateKey) => {
  try {
    const logKey = `${LOG_PREFIX}${dateKey}`;
    await sdk.deleteSecure(logKey);
    
    // X√≥a dateKey kh·ªèi danh s√°ch
    const currentList = await getLogDateList();
    const updatedList = currentList.filter(date => date !== dateKey);
    const jsonString = JSON.stringify(updatedList);
    await sdk.saveSecure(LOG_LIST_KEY, jsonString);
  } catch (error) {
    console.error('Error deleting logs by date:', error);
    // L∆∞u exception n√†y
    await saveLogException(error, { logSource: 'logs', logCategory: 'deleteLogsByDate' });
    throw error;
  }
};

/**
 * X√≥a t·∫•t c·∫£ logs
 * @returns {Promise<void>}
 */
export const deleteAllLogs = async () => {
  try {
    const dateList = await getLogDateList();
    
    // X√≥a t·∫•t c·∫£ log keys
    await Promise.all(dateList.map(dateKey => {
      const logKey = `${LOG_PREFIX}${dateKey}`;
      return sdk.deleteSecure(logKey);
    }));
    
    // X√≥a danh s√°ch dates
    await sdk.deleteSecure(LOG_LIST_KEY);
  } catch (error) {
    console.error('Error deleting all logs:', error);
    // L∆∞u exception n√†y
    await saveLogException(error, { logSource: 'logs', logCategory: 'deleteAllLogs' });
    throw error;
  }
};

/**
 * L·∫•y log theo ID
 * @param {string} logId - Log ID
 * @param {string} dateKey - Date key (YYYYMMDD), optional
 * @returns {Promise<Object|null>} Log object ho·∫∑c null
 */
export const getLogById = async (logId, dateKey = null) => {
  try {
    if (dateKey) {
      // T√¨m trong ng√†y c·ª• th·ªÉ
      const logs = await getLogsByDate(dateKey);
      return logs.find(log => log.id === logId) || null;
    } else {
      // T√¨m trong t·∫•t c·∫£ c√°c ng√†y
      const dateList = await getLogDateList();
      for (const date of dateList) {
        const logs = await getLogsByDate(date);
        const found = logs.find(log => log.id === logId);
        if (found) return found;
      }
      return null;
    }
  } catch (error) {
    console.error('Error getting log by ID:', error);
    await saveLogException(error, { logSource: 'logs', logCategory: 'getLogById' });
    return null;
  }
};

// Export default object
export default {
  getLogDateList,
  getLogsByDate,
  saveLog,
  saveLogException,
  deleteLogsByDate,
  deleteAllLogs,
  getLogById,
};
