// ==================== src/contexts/NavigationContext.jsx ====================
// Navigation Context v·ªõi Enhanced Swipe Gesture Support
// H·ªó tr·ª£: Touch swipe left/right/top/bottom cho navigation

import React, {
  createContext,
  useContext,
  useState,
  useCallback,
  useEffect,
  useRef,
} from "react";
import { useWebViewSDK } from "./WebViewSDKContext";
import { BOTTOM_SHEET_SCREENS, SCREEN_NAMES } from "@/constants/navigation";
import { saveLogException } from '@logs';

const NavigationContext = createContext(null);

export const NavigationProvider = ({ children }) => {
  const sdk = useWebViewSDK();

  // ==================== NAVIGATION STATE ====================
  const [currentScreen, setCurrentScreen] = useState("home");
  const [screenParams, setScreenParams] = useState({});
  const [stateScreenParams, _setStateScreenParams] = useState({}); // State c·ªßa m√†n h√¨nh hi·ªán t·∫°i (form state, etc.)
  const stateScreenParamsRef = useRef(stateScreenParams);
  // History gi·ªù l∆∞u c·∫£ screen, params v√† stateScreenParams: [{ screen: "home", params: {}, stateScreenParams: {} }, ...]
  const [history, setHistory] = useState([{ screen: "home", params: {}, stateScreenParams: {} }]);
  const [openModals, setOpenModals] = useState([]);

  // Wrapper: c·∫≠p nh·∫≠t ref ngay l·∫≠p t·ª©c tr∆∞·ªõc khi setState ƒë·ªÉ ƒë·∫£m b·∫£o navigate l·∫•y ƒë∆∞·ª£c gi√° tr·ªã m·ªõi nh·∫•t
  const setStateScreenParams = useCallback((next) => {
    const nextValue = typeof next === 'function' ? next(stateScreenParamsRef.current) : next || {};
    stateScreenParamsRef.current = nextValue;
    _setStateScreenParams(nextValue);
  }, []);

  // Keep ref in sync when state changes due to other effects
  useEffect(() => {
    stateScreenParamsRef.current = stateScreenParams;
  }, [stateScreenParams]);

  // Root screens (navigation tabs)
  const rootScreens = Object.values({
    home: SCREEN_NAMES.home
  });
  
  // ==================== RESPONSIVE STATE ====================
  const [deviceType, setDeviceType] = useState("mobile");
  const [headerWidth, setHeaderWidth] = useState("100%");

  // ==================== SWIPE GESTURE STATE ====================
  const [touchStart, setTouchStart] = useState(null);
  const [touchEnd, setTouchEnd] = useState(null);
  const [isSwiping, setIsSwiping] = useState(false);

  // Swipe gesture configuration
  const MIN_SWIPE_DISTANCE = 100; // Minimum distance for swipe detection

  // ==================== RESPONSIVE DETECTION ====================
  const updateDeviceType = useCallback(() => {
    const width = window.innerWidth;
    let type = "mobile";
    let maxWidth = "100%";

    console.log(`üì± [Navigation] Device: ${type}, Width: ${width}px, Header Max-Width: ${maxWidth}`);

    setDeviceType(type);
    setHeaderWidth(maxWidth);
  }, []);

  useEffect(() => {
    updateDeviceType();
    window.addEventListener("resize", updateDeviceType);
    window.addEventListener("orientationchange", () => {
      setTimeout(updateDeviceType, 100);
    });

    return () => {
      window.removeEventListener("resize", updateDeviceType);
      window.removeEventListener("orientationchange", updateDeviceType);
    };
  }, [updateDeviceType]);

  // ==================== NAVIGATION METHODS ====================

  const navigate = useCallback(
    (screen, params = {}) => {
      closeAllModals();
      console.log("‚û°Ô∏è [Navigation] Navigating to:", screen, params);

      // Helper function ƒë·ªÉ update history v·ªõi stateScreenParams hi·ªán t·∫°i (lu√¥n d√πng ref ƒë·ªÉ l·∫•y latest)
      const updateHistoryWithCurrentState = (prev) => {
        if (prev.length > 0) {
          const updated = [...prev];
          updated[updated.length - 1] = {
            ...updated[updated.length - 1],
            stateScreenParams: stateScreenParamsRef.current || {}
          };
          return updated;
        }
        return prev;
      };

      // T·∫°o history entry m·ªõi cho screen m·ªõi
      const historyEntry = { screen, params, stateScreenParams: {} };
      setCurrentScreen(screen);
      setScreenParams(params);
      _setStateScreenParams({}); // Reset stateScreenParams cho screen m·ªõi v√† sync ref d∆∞·ªõi useEffect

      // Ki·ªÉm tra BOTTOM_SHEET_SCREENS n·∫øu navigate ƒë·∫øn th√¨ reset l·∫°i history v√† currentScreen
      if (Object.values(BOTTOM_SHEET_SCREENS).includes(screen)) {
        // Reset history, kh√¥ng c·∫ßn l∆∞u state c·ªßa screen tr∆∞·ªõc
        setHistory([{ screen, params, stateScreenParams: {} }]);
        return;
      }

      if (rootScreens.includes(screen)) {
        // Reset history, kh√¥ng c·∫ßn l∆∞u state c·ªßa screen tr∆∞·ªõc
        setHistory([{ screen, params, stateScreenParams: {} }]);
        console.log("üè† [Navigation] Root screen - History reset");
      } else {
        // L∆∞u stateScreenParams c·ªßa screen hi·ªán t·∫°i v√† th√™m entry m·ªõi
        setHistory((prev) => {
          const updated = updateHistoryWithCurrentState(prev);
          return [...updated, historyEntry];
        });
        console.log("üìö [Navigation] Detail screen - Added to history");
      }
    },
    [rootScreens]
  );

  const goBack = useCallback(() => {
    console.log("üîô [Navigation] Go back requested");
    // Priority 1: Close modal if any
    if (openModals.length > 0) {
      console.log("üî≤ [Navigation] Closing modal instead");
      closeModal();
      return true;
    }

    // Ch·∫∑n 1 l·∫ßn back v·ªÅ home stack cu·ªëi
    if (history.length === 1) {
      return false;
    }
    
    // C√°c stack history kh√°c s·∫Ω goback v·ªÅ
    if (history.length > 1) {
      // L∆∞u stateScreenParams hi·ªán t·∫°i v√†o history entry tr∆∞·ªõc khi goBack (d√πng ref ƒë·ªÉ l·∫•y latest)
      setHistory((prev) => {
        if (prev.length > 0) {
          const updated = [...prev];
          updated[updated.length - 1] = {
            ...updated[updated.length - 1],
            stateScreenParams: stateScreenParamsRef.current || {}
          };
          return updated;
        }
        return prev;
      });

      const newHistory = [...history];
      newHistory.pop();
      const previousEntry = newHistory[newHistory.length - 1];

      console.log("‚úÖ [Navigation] Going back to:", previousEntry.screen, "with params:", previousEntry.params, "with state:", previousEntry.stateScreenParams);
      setHistory(newHistory);
      setCurrentScreen(previousEntry.screen);
      setScreenParams(previousEntry.params || {});
      _setStateScreenParams(previousEntry.stateScreenParams || {});
      return true;
    }

    console.log("‚ö†Ô∏è [Navigation] Cannot go back - at root");
    return false;
  }, [history, openModals]);

  useEffect(() => {
    console.log('history change', history);
  }, [history]);

  const isRootScreen = useCallback(() => {
    return rootScreens.includes(currentScreen);
  }, [currentScreen, rootScreens]);

  const canGoBack = useCallback(() => {
    return history.length > 1 || openModals.length > 0 || isRootScreen();
  }, [history, openModals, isRootScreen]);

  // ==================== MODAL MANAGEMENT ====================

  const openModal = useCallback((modalId) => {
    console.log("üî≤ [Navigation] Opening modal:", modalId);
    setOpenModals((prev) => [...prev, modalId]);
  }, []);

  const closeModal = useCallback(() => {
    console.log("üî≤ [Navigation] Closing modal");
    setOpenModals((prev) => {
      const newModals = [...prev];
      newModals.pop();
      return newModals;
    });
  }, []);

  const closeAllModals = useCallback(() => {
    console.log("üî≤ [Navigation] Closing all modals");
    setOpenModals([]);
  }, []);

  const hasOpenModal = useCallback(() => {
    return openModals.length > 0;
  }, [openModals]);

  // ==================== SWIPE GESTURE HANDLERS ====================

  /**
   * Handle Native Swipe Back (t·ª´ iOS/Android)
   */
  const handleSwipeBack = useCallback(() => {
    console.log("üëÜ [Navigation] Native Swipe Back detected");

    const handled = goBack();

    if (!handled) {
      console.log("üì± [Navigation] Cannot go back - calling native BACK_APP");
      if (sdk.isNativeWebView()) {
        sdk.backToApp();
      }
    }
  }, [goBack, sdk]);

  /**
   * Handle Touch Start - Swipe b·∫Øt ƒë·∫ßu
   */
  const handleTouchStart = useCallback((e) => {
    const touch = e.touches[0];

    setTouchStart({
      x: touch.clientX,
      y: touch.clientY,
      time: Date.now()
    });
    setTouchEnd(null);
    setIsSwiping(true);
  }, []);

  /**
   * Handle Touch Move - Swipe ƒëang di·ªÖn ra
   */
  const handleTouchMove = useCallback((e) => {
    if (!isSwiping || !touchStart) return;
    const isCarousel = e.target.closest('.react-multi-carousel-list');
    const isGroupCategories = e.target.closest('.group-categories-items-container');
    const isHomeUtilities = e.target.closest('.home-utilities-container');
    
    const ignoreHandle = isCarousel || isGroupCategories || isHomeUtilities;
    
    if (ignoreHandle) {      
      return; // Kh√¥ng x·ª≠ l√Ω n·∫øu c√≥ d√πng scroll b√™n trong
    }

    const touch = e.touches[0];
    setTouchEnd({
      x: touch.clientX,
      y: touch.clientY,
      time: Date.now()
    });
  }, [isSwiping, touchStart]);

  /**
   * Handle Touch End - Swipe k·∫øt th√∫c
   */
  const handleTouchEnd = useCallback(() => {
    if (!touchStart || !touchEnd || !isSwiping) {
      setIsSwiping(false);
      return;
    }

    const deltaX = touchEnd.x - touchStart.x;
    const deltaY = touchEnd.y - touchStart.y;
    const deltaTime = touchEnd.time - touchStart.time;

    // Reset state
    setIsSwiping(false);
    setTouchStart(null);
    setTouchEnd(null);

    const isHorizontalSwipe = Math.abs(deltaX) > Math.abs(deltaY);
    const isVerticalSwipe = Math.abs(deltaY) > Math.abs(deltaX);
    const isValidDistance = Math.abs(deltaX) > MIN_SWIPE_DISTANCE || Math.abs(deltaY) > MIN_SWIPE_DISTANCE;
    const isValidSpeed = deltaTime < 500;

    if (!isValidDistance || !isValidSpeed) {
      return;
    }

    if (isVerticalSwipe) {
      const swipeDirection = deltaY > 0 ? "bottom" : "top";
      console.log(`üëÜ [Swipe] Detected: ${swipeDirection.toUpperCase()} (${Math.abs(deltaY)}px in ${deltaTime}ms)`);
      if (swipeDirection === "bottom") {
        console.log("üëÜ [Swipe] TOP ‚Üí BOTTOM detected");
      } else if (swipeDirection === "top") {
        console.log("üëÜ [Swipe] BOTTOM ‚Üí TOP detected");
      }
    }
  }, [touchStart, touchEnd, isSwiping]);

  // ==================== CONTEXT VALUE ====================

  const value = {
    // State
    currentScreen,
    screenParams,
    stateScreenParams,
    setStateScreenParams,
    history,
    rootScreens,
    openModals,

    // Responsive State
    deviceType,
    headerWidth,

    // Navigation methods
    navigate,
    goBack,
    isRootScreen,
    canGoBack,

    // Modal methods
    openModal,
    closeModal,
    closeAllModals,
    hasOpenModal,

    // Swipe gesture state
    isSwiping,

    // Native Swipe Back
    handleSwipeBack,
  };

  return (
    <NavigationContext.Provider value={value}>
      {children}
    </NavigationContext.Provider>
  );
};

export const useNavigation = () => {
  const context = useContext(NavigationContext);
  if (!context) {
    const error = new Error("useNavigation must be used within NavigationProvider");
    
    // Log exception tr∆∞·ªõc khi throw
    saveLogException(error, {
      logSource: 'context',
      logCategory: 'navigation',
      metadata: { hook: 'useNavigation' }
    }).catch(logError => {
      console.error('Failed to log exception:', logError);
    });
    
    throw error;
  }
  return context;
};

export default NavigationProvider;