// ==================== src/components/SplashScreen.jsx ====================
import React, { useEffect, useState } from 'react';
import { useWebViewSDK, useLanguage, useService } from '@/contexts';
import { saveLogException } from '@logs';

const SplashScreen = ({ onComplete }) => {
  const sdk = useWebViewSDK();
  const { setLanguage } = useLanguage();
  const { handleError } = useService();
  const [isValidating, setIsValidating] = useState(false);

  useEffect(() => {
    // Check mock mode is null, set to false
    if (!localStorage.getItem('IS_MOCK_MODE')) {
      localStorage.setItem('IS_MOCK_MODE', 'false');
    }

    // // Delay 1s tr∆∞·ªõc khi b·∫Øt ƒë·∫ßu validate
    const timer = setTimeout(() => {
      // validateAndComplete();

      // Fake validate token success
      onComplete();
    }, 1000);
    return () => clearTimeout(timer);
  }, []);


  /**
   * Validate token v√† g·ªçi onComplete n·∫øu th√†nh c√¥ng
   */
  const validateAndComplete = () => {
    setIsValidating(true);

    // Set PARENT_PARAMS t·ª´ WEB_TO_SDK ho·∫∑c NATIVE_PARAMS
    if (!sdk.isNativeWebView()) {
      window.PARENT_PARAMS = window.WEB_TO_SDK;
    } else {
      window.PARENT_PARAMS = window.NATIVE_PARAMS;
    }
    console.log('üåç [SplashScreen] PARENT_PARAMS set:', window.PARENT_PARAMS);

    // Parse token t·ª´ params (l√† JSON string)
    let token = null;
    try {
      const tokenString = window.PARENT_PARAMS?.token;
      if (tokenString) {
        token = typeof tokenString === 'string' ? JSON.parse(tokenString) : tokenString;
        console.log('üîë [SplashScreen] Parsed token:', token);
      }
    } catch (error) {
      console.error('‚ùå [SplashScreen] Error parsing token:', error);
      
      // Log exception tr∆∞·ªõc khi x·ª≠ l√Ω
      saveLogException(error, {
        logSource: 'component',
        logCategory: 'splash_screen',
        metadata: { action: 'parseToken' }
      }).catch(logError => {
        console.error('Failed to log exception:', logError);
      });
      
      handleError(new Error(`Failed to parse token: ${error.message}`));
      setIsValidating(false);
      return; // Kh√¥ng ti·∫øp t·ª•c n·∫øu parse token l·ªói
    }

    // Normalize token structure v√† ki·ªÉm tra success
    if (token) {
      // X·ª≠ l√Ω tr∆∞·ªùng h·ª£p response c√≥ nested data object
      let normalizedToken = token;
      if (token.data && typeof token.data === 'object') {
        normalizedToken = {
          ...token,
          ...token.data, // Flatten data object v√†o token
          success: token.success !== false // N·∫øu kh√¥ng c√≥ success field, coi nh∆∞ true n·∫øu c√≥ data
        };
      } else {
        // N·∫øu kh√¥ng c√≥ success field, ki·ªÉm tra xem c√≥ accessToken ho·∫∑c domain kh√¥ng
        if (token.success === undefined) {
          normalizedToken = {
            ...token,
            success: !!(token.accessToken || token.domain) // Coi nh∆∞ success n·∫øu c√≥ accessToken ho·∫∑c domain
          };
        }
      }

      // Ki·ªÉm tra token.success tr∆∞·ªõc khi ti·∫øp t·ª•c
      if (!normalizedToken.success) {
        console.error('‚ùå [SplashScreen] Token is not successful:', normalizedToken);
        const errorMessage = normalizedToken.error || normalizedToken.message || 'Token validation failed';
        handleError(new Error(`Token is not valid: ${errorMessage}`));
        setIsValidating(false);
        return; // Kh√¥ng ti·∫øp t·ª•c n·∫øu token kh√¥ng valid
      } else {
        // L∆∞u normalized token v√†o PARENT_PARAMS ƒë·ªÉ base.jsx c√≥ th·ªÉ s·ª≠ d·ª•ng
        window.PARENT_PARAMS.token = normalizedToken;
        console.log('‚úÖ [SplashScreen] Token validated and saved:', {
          success: normalizedToken.success,
          hasAccessToken: !!normalizedToken.accessToken,
          hasDomain: !!normalizedToken.domain
        });
      }
    } else {
      console.warn('‚ö†Ô∏è [SplashScreen] No token found in PARENT_PARAMS');
      handleError(new Error('No token found in PARENT_PARAMS'));
      setIsValidating(false);
      return; // Kh√¥ng ti·∫øp t·ª•c n·∫øu kh√¥ng c√≥ token
    }

    // Set language from params
    const lang = window.PARENT_PARAMS?.lang;
    console.log('üåç [SplashScreen] Lang from params:', lang);
    if (lang && (lang === 'vi' || lang === 'en')) {
      console.log('üåç [SplashScreen] Calling setLanguage with:', lang);
      setLanguage(lang);
    }

    // N·∫øu t·∫•t c·∫£ ƒë·ªÅu OK, g·ªçi onComplete
    setIsValidating(false);
    onComplete();
  };

  return (
    <>
      <div className="splash-screen">
        <div className="splash-content">
          <div className="splash-loader">
            {[0, 1, 2].map(i => (
              <div
                key={i}
                className="loader-dot"
                style={{
                  animationDelay: `${-0.32 + i * 0.16}s`
                }}
              />
            ))}         
          </div>
        </div>
      
      <style jsx>{`
        .splash-screen {
          position: fixed;
          inset: 0;
          z-index: 9999;
          display: flex;
          align-items: center;
          justify-content: center;
          background-image: url('/images/default_background_image.png');
          background-size: 100% 100%;
          background-position: center;
          background-repeat: no-repeat;
        }
        
        .splash-content {
          text-align: center;
          animation: fadeInUp 0.8s ease;
        }
        
        .splash-logo {
          margin-bottom: 24px;
          animation: pulse 2s ease-in-out infinite;
        }
        
        .splash-loader {
          display: flex;
          gap: 8px;
          justify-content: center;
        }
        
        .loader-dot {
          width: 10px;
          height: 10px;
          background: #96CCFF;
          border-radius: 50%;
          animation: bounce 1.4s ease-in-out infinite, colorPulse 1.4s ease-in-out infinite;
        }
        
        @keyframes colorPulse {
          0%, 100% {
            background: #00ab50;
            box-shadow: 0 0 0 0 rgba(0, 149, 217, 0.7);
          }
          50% {
            background: #0095d9;
            box-shadow: 0 0 8px 4px rgba(0, 171, 80, 0.5);
          }
        }
        
        @keyframes colorPulseFade {
          0%, 100% {
            background: rgba(0, 149, 217, 0.5);
            box-shadow: 0 0 0 0 rgba(0, 149, 217, 0.3);
          }
          50% {
            background: rgba(0, 171, 80, 0.8);
            box-shadow: 0 0 8px 4px rgba(0, 171, 80, 0.4);
          }
        }
        
        @keyframes bounce {
          0%, 100% {
            transform: translateY(0);
          }
          50% {
            transform: translateY(-8px);
          }
        }
        
        @keyframes fadeInUp {
          from {
            opacity: 0;
            transform: translateY(20px);
          }
          to {
            opacity: 1;
            transform: translateY(0);
          }
        }
        
        @keyframes pulse {
          0%, 100% {
            opacity: 1;
            transform: scale(1);
          }
          50% {
            opacity: 0.8;
            transform: scale(0.98);
          }
        }
      `}</style>
      </div>
    </>
  );
};

export default SplashScreen;