// useAudioPlayer.js - Version c·∫£i ti·∫øn
import { useRef, useCallback, useEffect, useState } from 'react';

export const useAudioPlayer = () => {
  const audioRef = useRef(null);
  const [isInitialized, setIsInitialized] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [volume, setVolumeState] = useState(0);
  const [error, setError] = useState(null);
  const retryCountRef = useRef(0);
  const maxRetries = 60;

  const initAudio = useCallback((url, autoplay = true, loop = true) => {
    setError(null);
    retryCountRef.current = 0;

    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current = null;
    }

    if (!url) {
      const err = 'URL kh√¥ng ƒë∆∞·ª£c ƒë·ªÉ tr·ªëng';
      setError(err);
      console.error(err);
      return;
    }

    audioRef.current = new Audio();
    audioRef.current.loop = loop;
    
    // Load muted state from localStorage and set initial volume
    const MUTED_KEY = 'game-sdk-muted';
    const savedMuted = localStorage.getItem(MUTED_KEY);
    const isMuted = savedMuted ? JSON.parse(savedMuted) : false;
    const initialVolume = isMuted ? 0 : volume;
    
    audioRef.current.volume = initialVolume;
    setVolumeState(initialVolume);
    
    // Store audio instance in window object for shared access
    window.audioInstance = audioRef.current;

    const handleLoadedMetadata = () => {
      setDuration(audioRef.current.duration);
      console.log('Audio loaded successfully');
    };

    const handleTimeUpdate = () => {
      setCurrentTime(audioRef.current.currentTime);
    };

    const handlePlay = () => {
      setIsPlaying(true);
      retryCountRef.current = 0; // Reset retry count on success
    };

    const handlePause = () => {
      setIsPlaying(false);
    };

    const handleEnded = () => {
      setIsPlaying(false);
      if (!audioRef.current.loop) {
        setCurrentTime(0);
      }
    };

    const handleError = (e) => {
      const errorMessages = {
        1: 'MEDIA_ERR_ABORTED: T·∫£i audio b·ªã h·ªßy',
        2: 'MEDIA_ERR_NETWORK: L·ªói m·∫°ng khi t·∫£i audio',
        3: 'MEDIA_ERR_DECODE: L·ªói gi·∫£i m√£ audio',
        4: 'MEDIA_ERR_SRC_NOT_SUPPORTED: ƒê·ªãnh d·∫°ng audio kh√¥ng ƒë∆∞·ª£c h·ªó tr·ª£ ho·∫∑c file kh√¥ng t·ªìn t·∫°i'
      };

      const errorCode = audioRef.current?.error?.code;
      const errorMsg = errorMessages[errorCode] || 'L·ªói kh√¥ng x√°c ƒë·ªãnh khi load audio';
      
      console.error('L·ªói audio:', {
        message: errorMsg,
        code: errorCode,
        url: url,
        event: e
      });

      setError(errorMsg);
      setIsInitialized(false);
    };

    const handleCanPlay = () => {
      console.log('Audio c√≥ th·ªÉ ph√°t');
      setIsInitialized(true);
    };

    // Retry autoplay v·ªõi delay
    const tryAutoplay = () => {
      if (!autoplay) return;

      // Check muted state from localStorage - don't autoplay if muted
      const MUTED_KEY = 'game-sdk-muted';
      const savedMuted = localStorage.getItem(MUTED_KEY);
      const isMuted = savedMuted ? JSON.parse(savedMuted) : false;
      
      if (isMuted) {
        console.log('Audio muted, skipping autoplay');
        // Ensure volume is 0 and audio is paused
        if (audioRef.current) {
          audioRef.current.volume = 0;
          if (!audioRef.current.paused) {
            audioRef.current.pause();
          }
        }
        return;
      }

      audioRef.current.play()
        .then(() => {
          console.log('Autoplay th√†nh c√¥ng');
        })
        .catch(err => {
          retryCountRef.current++;
          
          if (err.name === 'NotAllowedError') {
            console.warn(`Autoplay b·ªã ch·∫∑n (l·∫ßn th·ª≠ ${retryCountRef.current}/${maxRetries})`);
            
            // Retry sau m·ªôt kho·∫£ng th·ªùi gian
            if (retryCountRef.current < maxRetries) {
              setTimeout(() => {
                console.log('ƒêang retry autoplay...');
                tryAutoplay();
              }, 1000 * retryCountRef.current); // TƒÉng d·∫ßn th·ªùi gian retry
            } else {
              console.warn('ƒê√£ h·∫øt s·ªë l·∫ßn retry. Ch·ªù user interaction.');
              setError('Vui l√≤ng t∆∞∆°ng t√°c ƒë·ªÉ ph√°t nh·∫°c (tap v√†o m√†n h√¨nh)');
              
              // Listen for user interaction
              const handleInteraction = () => {
                audioRef.current?.play().catch(console.error);
                document.removeEventListener('touchstart', handleInteraction);
                document.removeEventListener('click', handleInteraction);
              };
              
              document.addEventListener('touchstart', handleInteraction, { once: true });
              document.addEventListener('click', handleInteraction, { once: true });
            }
          } else {
            console.error('Autoplay failed:', err);
            setError(err.message);
          }
        });
    };

    audioRef.current.addEventListener('loadedmetadata', handleLoadedMetadata);
    audioRef.current.addEventListener('timeupdate', handleTimeUpdate);
    audioRef.current.addEventListener('play', handlePlay);
    audioRef.current.addEventListener('pause', handlePause);
    audioRef.current.addEventListener('ended', handleEnded);
    audioRef.current.addEventListener('error', handleError);
    audioRef.current.addEventListener('canplay', handleCanPlay);

    // Set src v√† load
    audioRef.current.src = url;
    audioRef.current.load();

    // Try autoplay sau khi load
    if (autoplay) {
      audioRef.current.addEventListener('canplay', tryAutoplay, { once: true });
    }

    // Expose ƒë·ªÉ native code c√≥ th·ªÉ g·ªçi
    window.playBackgroundAudio = () => {
      if (audioRef.current) {
        audioRef.current.play().catch(err => {
          console.error('Manual play failed:', err);
        });
      }
    };

    return () => {
      if (audioRef.current) {
        audioRef.current.removeEventListener('loadedmetadata', handleLoadedMetadata);
        audioRef.current.removeEventListener('timeupdate', handleTimeUpdate);
        audioRef.current.removeEventListener('play', handlePlay);
        audioRef.current.removeEventListener('pause', handlePause);
        audioRef.current.removeEventListener('ended', handleEnded);
        audioRef.current.removeEventListener('error', handleError);
        audioRef.current.removeEventListener('canplay', handleCanPlay);
      }
      delete window.playBackgroundAudio;
      // Don't delete window.audioInstance here because it's shared
    };
  }, [volume]);

  // ... rest of the methods (start, stop, pause, resume, etc.)
  const start = useCallback(() => {
    if (!isInitialized || !audioRef.current) {
      console.error('Audio ch∆∞a ƒë∆∞·ª£c kh·ªüi t·∫°o');
      return Promise.reject('Audio not initialized');
    }
    audioRef.current.currentTime = 0;
    return audioRef.current.play();
  }, [isInitialized]);

  const stop = useCallback(() => {
    if (!audioRef.current) return;
    audioRef.current.pause();
    audioRef.current.currentTime = 0;
    setCurrentTime(0);
  }, []);

  const pause = useCallback(() => {
    if (!audioRef.current) return;
    audioRef.current.pause();
  }, []);

  const resume = useCallback(() => {
    if (!audioRef.current) {
      return Promise.reject('Audio not initialized');
    }
    return audioRef.current.play();
  }, []);

  const setVolume = useCallback((newVolume) => {
    const clampedVolume = Math.max(0, Math.min(1, newVolume));
    setVolumeState(clampedVolume);
    // Use window.audioInstance if available (shared instance), otherwise use local audioRef
    const audioInstance = window.audioInstance || audioRef.current;
    if (audioInstance) {
      audioInstance.volume = clampedVolume;
    }
  }, []);

  useEffect(() => {
    return () => {
      if (audioRef.current) {
        audioRef.current.pause();
        audioRef.current = null;
      }
    };
  }, []);

  return {
    initAudio,
    start,
    stop,
    pause,
    resume,
    setVolume,
    isInitialized,
    isPlaying,
    currentTime,
    duration,
    volume,
    error
  };
};
