// BackgroundMusic.jsx
import { useAudioPlayer } from '@/hooks/AudioPlayer';
import { useEffect } from 'react';

function BackgroundMusic({ 
  url, 
  autoplay = true, 
  loop = true, 
  volume = 1.0 
}) {
  const { initAudio, setVolume } = useAudioPlayer();

  useEffect(() => {
    const cleanup = initAudio(url, autoplay, loop);
    return cleanup;
  }, [url, autoplay, loop, initAudio]);

  // Update volume khi volume prop thay ƒë·ªïi
  useEffect(() => {
    setVolume(volume);
  }, [volume, setVolume]);

  return null; // Kh√¥ng render g√¨
}

export default BackgroundMusic;